module.exports = [{"name":"Mt lt nc mt - Kito Aya","content":"Nu mt ngy, bn nhn ra bn ang mt dn kh nng kim sot c th ca bn, bn khng th ni, khng th i li, khng th lm nhng vic tng chng rt nh nht; bn nhn ra bn chnh l gnh nng ca mi ngi  gia nh, bn b,.. v ngi bn yu nht; bn nhn ra tng lai trc mt  ng sp li vi bn,.. Bn s ra sao ? Vi Aya, vic bc i nh mt ngi bnh thng m khng b vp ng hay vn vo ngi khc  i, l c mt gic m thnh hin thc, hay ch cn c i xem phim vi bn b, quy qun cng gia nh, hay n thun ch l cm bt vit..  l rt hnh phc. c m ca Aya gin d nh th, c ch mong c n trng bng i chn ca mnh, c th vui v vi bn b, v c th.. kt hn vi ngi c yu.. Aya  phi tri qua nhng cm gic rt kh khn, t vic c gng gi thng bng  khi ng, iu khin tay chn ca mnh, t ngi, t ng, hay c gng khng khc khi mi ngi nhn c bc i nng nhc trn ng vi nh mt thng hi, thm ch trong vic ni chuyn.. n lc c ch cn c th nm trn ging bnh.. cho n pht cui cng...Nhng ngy thng cui cng, Aya khng cn c th ni chuyn r rng na, c bp b tng ting tht kh khn nhng vn c gng truyn t  nguyn ca mnh. Ngy ngy c vn cm ci vit nht k tng nt ch nguch ngoc n mc ng gc trn ging bnh.. Ch cn c sng, c bc i l c mt hnh phc ri, bn c bit khng ?","quote":"C nhng ngi m s tn ti ca h ging nh khng kh, m du, nh nhng, ch khi h mt i ngi ta mi nhn ra h quan trng nhng no. Mnh mun tr thnh mt s tn ti nh th."},{"name":"Git con chim nhi - Harper Lee","content":"Cu chuyn k v 2 a tr sng ti mt th trn nh  min nam nc M. Xuyn sut cun sch l cuc sng trong 3 nm u th xung quanh 2 anh em Scout v Jem, lng ghp vo  l nhng cu chuyn v s ch k, s th hn, lng dng cm, s kiu hnh v nhng nh kin. Trong 'Git con chim nhi', bn s thy Atticus l mt lut s bo v thn ch ca mnh khi m dng gin d m khng h dng ti bo lc. ng s dng sc mnh ca li ni v dng nng lc ca bn thn  ng vng y kiu ngo trc nhng li lng m. ng thi, bn cng thy nhng a tr  c nhiu quyt nh sai lm, v t bn thn chng phi gii quyt nhng hu qu do mnh gy ra. Mc d cun sch c vit t nhng nm 1930, nhiu vn   tr nn li thi, nhng 'Git con chim nhi' vn kh st vi thc t. Nhng vn  m nhn vt chnh ca chng ta gp phi trong trng hc hay x hi u rt chn thc, lin quan ti c vn  gii tnh v la tui. Cun sch cng c vit di gc nhn ca mt a tr, nn cc vn  u tr nn d hiu hn rt nhiu.","quote":"C rt nhiu th xu xa trn th gii ny, con trai . Cha c rng mnh c th gi chng trnh con tht xa. Nhng iu  l khng th"},{"name":"Rng Nauy - Murakami Haruki","content":"Cng thot thai t ni bun thng trong sng v tn ti, Rng Na Uy, bi ht nm no ca Beatles,  c ly lm tn gi cho cun tiu thuyt tnh yu ngt ngo v u su ca Haruki Murakami. Bc vo ci sng ca Rng Na Uy, qua s sm c n nh nh mnh ca nhng ngi tr tui, qua mi tnh tay ba va qun qut xc thn va u mc su bi ca Naoko-Toru-Midori, ngi ta cm thy ng ngng trc tnh yu nh l ni tr ng duy nht ca ngi n ng v ngi n b trn th gian ny, v khm ph ra mt ni bun mnh mang, trng vng rt Nht Bn ca thi hin i. Trong ni u t v c n nh mt nh mnh  ci t ni nhng ngi mi ln, trong s tuyt vng ca nhng tm hn trong sng sn sng hy sinh thn mnh  khi tho hip vi cuc sng th gian. V tnh yu  l ni tr ng duy nht. tnh yu v s gii phng ca xc thn bao bc ly n, lm cho ngi n ng v ngi n b c th yu nhau vi tt c nhng g c th trc cuc i ngn ngi v qu gi. Vi  ngha , mi tnh tay ba Naoko-Toru-Midori  lay ng hng chc triu c gi trn ton th gii trong mt tc phm c coi l tuyt bt ca Murakami.","quote":"Yu ngi khc l mt iu tuyt vi, v nu tnh yu y l chn thnh, chng ai cui cng li b nm vo 1 m cung c. Cu phi thnh tht vi chnh mnh hn mi c"},{"name":"Khc gia Si Gn - Nguyn Ngc Thch ","content":"Bc tranh hin thc, vi nhng gam mu xm lnh v Si Gn ca tc gi chuyn v nhng  ti gai gc, Nguyn Ngc Thch.<br/>Su con ngi gia t Si Gn. <br />Phan i tm s h h bng chin thng, chinh phc nhng nh cao x hi.<br />Nam lc trong ni au, m chm trong nhng o tng do bn thn dng ln.<br />T, nhn i bng con mt lnh lng, am hiu v n b n kinh khng.<br />M, nghin cu tm l  ri s hi nhn ra s tht mnh tm c.<br />Thy, chi vi, bp bnh gia hai b gii tnh.<br />n, sng o, quan tm n mng x hi hn cm xc ngi xung quanh.<br />H, c mt im chung duy nht l chn Si Gn lm t sng. B m nh bi bi xe, bi n Si Gn xanh xanh  , bi nhng qun caf, bi nhng  bnh m b m nh bi kht khao tm cho c p n cho cu hi, 'Ti l ai?'<br />H, c Si Gn gn kt, sp xp  an xch h vo i nhau,  cng tri qua nhng hoan lc, chia ly.  ri n cui cng, li c ng cng nhau, ch n gin l khc, cho h h lng, cho tan ni au.<br />H, ngi i k , nhng vn tin rng c ngy gp li, ch cn mt nim tin nh nhoi  vn gi ngi ta ch nhau ch nh chuyn Si Gn.","quote":" Si Gn khi nim thn v l cch nhau bi mt t giy gi l tin"},{"name":"Catalonia - Tnh yu ca ti - George Orwell","content":"Tc phm k v giai on ni chin  Ty Ban Nha m George Orwell tng tham gia,trong  c nhng trang vit v cng xc ng ni v tnh ng ch , ng i H xem nhau nh nhng ngi bn, nhng ngi anh em cng chung l tng, m trn ht l tinh thn cch mng qun bnh gia nhng s quan v binh s, khng c s phn bit ng cp, i x bt bnh ng. Chnh n  to cho George Orwell suy ngh v mt x hi tt p, mt x hi phi giai cp m trc y ng cha tng bit n.Cng thng qua tc phm ny, Orwell mun gi gm n c gi phn no tnh cch ca con ngi v ca thin nhin Ty Ban Nha, vi nhng on vn lm cho ngi c xc ng, chan cha tnh yu i vi min t y au thng ny.","quote":"Ngi Ty Ban Nha gii nhiu vic, nhng nh nhau th rt km. S vng v v trn ht l s ty tin ca h  lm tt c ngi ngai quc phi kinh ngc."},{"name":"Cho ti xin mt v i tui th - Nguyn Nht nh","content":"Ti  tng bit: i vi tr nh vn vt th gian u l vn , vn vt th gian cng chnh l li gii p. Nu iu g trong mt tr nh c vn  th tht s l n khng n ri. Th nn, hy dng li, mt cht thi,  lng nghe tr nh,  thc s hiu chng cn v mun g. Chng chnh l nhng ch nhn tng lai ca th gii, v bn hy suy xt tht k  gio dc tt nhng v ny nh!<br/>Vi s xen k gia nhng k c qu kh ca thng cu Mi 8 tui v suy ngh ca mt ng Mi trng thnh  c gia nh, Nguyn Nht nh cho ta thy cng mt s vic m gc nhn ca mt a tr v mt ngi ln thc s qu khc bit. Nhng hnh nh so snh vui nhn, tnh tit nh nhng nhng khng km phn 'thu ht',... s cho bn nhng pht giy th gin tuyt vi.<br />Cho ti xin mt v i tui th tht chnh l mt chic v qu tuyt vi, cho chuyn hnh trnh v li sn ga tui nh.  ta thy nhng ngy thng m m, hnh phc bn gia nh.  thy li nhng ngi bn nh ngy xa cng ta ln ln di khong tri m c. V  thy li nhng hn nhin ngy th, s rc r v trong lnh ca tui nh.","quote":"Nhiu ngi s ni bun. Nhng ti khng s. Ti ch s mt cuc sng khng bun khng vui, ni chung l nht nho. i khi chng ta cng cn c ni bun lm bn, nht l lc cuc sng bng dng trng tri v cm gic c c xm chim ta tng pht."},{"name":"D khc - Ploy","content":"D Khc ly bi cnh cung nh hong tht, to nhn vt chnh l cng cha - n hong  nhng li chng c nhng ngt ngo lng mn nh kiu cng cha ng su trong rng i n hn ca hong t p trai. Lc cn l cng cha, c sng theo ng ngha ca 1 nng cng cha trong th gii t to ca chnh mnh. Phng khong & m mng t cch yu cho n cch sng.<br/>Hong t ca c, cng lng t v ho hoa nh trong truyn c tch. Lc tr thnh n hong, c bin cuc sng ca mnh ging nh ly cafe en khng ng, l tr chim chn b no cm xc. C chn ngi chng ca mnh theo ng danh ngha  Phu qun ca N hong. Ch l c khng ng c, cafe en d khng c ng nhng khi v ng tan bin cng  li cht v ngt trn u mi. Hnh phc ca tng nhn vt trong D Khc chnh l mt cuc trao i y tnh thng mi, nh i au kh ca ngi ny  ly hnh phc ca 1 ngi kia.<br/>Ct cht con ngi vo nhng trch nhim v nhng tnh cm ko nh ngha c. Tuy nhin, cha i n bc cui ca i ngi s khng bit ngi no mi l ngi hnh phc nht. Cng nh tng nhn vt trong truyn, khng c n chng cui s khng bit rt cuc nhn vt no c c hnh phc trn vn","quote":"Tnh yu l khi vt cht v hnh xinh p nht, huyn o nht trn gian. Nhng n ch l mt php tnh trn bn c cuc sng."},{"name":"Nam tc trn cy - Italo Calvino","content":"'Nam tc trn cy' , khi bt u cu chuyn mi 12 tui, ngi  t chi mn c sn do b ch u su Battista chun b vo ngy 15 thng Su nm 1767. B b m dn p v vi quyt tm mnh lit khng thm ng n mn n ny, Cosimo  ri khi bn n v tro ln mt ci cy trong vn nh. iu m b m cu ng ch l mt tr a thong qua  tr thnh mt cuc chin thc s chng li cc quy tc ca tng lp qu tc cng nh th gii ngi ln, bi Cosimo  a ra quyt nh k cc l khng bao gi ri khi ci cy ca mnh na. T khonh khc y, Cosimo  t t chc li mi th  c th sng trn cy tin nghi nht c th. Cu hc cch chuyn t cnh ny sang cnh khc, hc cch sn bn  c  n, hc cch dng mt th ging nh ci lu  trnh rt cng nhng bin i thi tit tht thng.<bt/>Cu chuyn c vit ging nh mt truyn k mang tnh trit hc kiu Voltaire ny n vi chng ta qua li k ca cu em Biagio, ngi vn va khm phc li va trn ngp cm gic tic nui cho anh trai mnh v lun gng tm cch hiu cch c x cc oan ca Cosimo. Cu i n cng kht vng ca mnh v iu y  lm nn ton b s phong ph cho cu chuyn ny.  l mt s phn tht k l, khin cu chuyn tr nn ht sc th v vi s in cung du ngt ca tui th, cht th cng nh tnh kiu hnh ca v nam tc tr.","quote":"T nhiu nm nay ti sng v nhng l tng m mnh cng khng bit gii thch cho chnh mnh"},{"name":"Lng chng c n - Nguyn Ngc Thch","content":"'i ai m chng tng c n mt ln.' - Cu ni ca a bn khi ngi trong qun Caf ng nght ngi. Nhn xung quanh, thy ai cng ang m kh kh ci in thoi, ipad trong tay D l ngi chung vi ngi thn, bn b, mi ngi vn gi mt th gii ring, nhng u  vn c cm gic khng ni thnh li. X hi g m l lng vy? Gia c bin ngi, sao vn c thy c n? Bn cnh mt ngi, m lng vn thy trng vngCht gi tt c l Lng chng c n. hng ngy vit Lng chng c n, cht thy nh bn thn c tr li hn chc tui. c quay v vi mi tnh u, nm tri mi v ca gin hn, ngt ngo v chia ly. Ri li thy au. Trong Lng chng c n, khng c bt c i t nhn xng ti no c dng. V chng phi vit cho bn thn, m vit cho chng ta, nhng ngi tr, nhng ngi hoang mang trong cuc i, hoang s trong tnh yu. Nhng ngi m t nht mt ln trong i thy lc lng, chi vi d xung quanh l nght cng ngi v ngi. Nhng ngi nghe c n ng thnh git, ln di trn khe mt,  ri sng ngy mai, li phi mm ci, v hiu rng mnh chng thng mnh th cng chng ai thng. Hi vng,Lng chng c n khng khi dy ni au no trong bn ... ","quote":"Ngi ta ni, c n lu ngy lm tri tim chai sn, thy khng ng cht no. C n lu ngy, lm tim yu mm hn, d p lon nhp khi c mt c ch n cn, quan tm bt cht. Ri li t dn vt mnh gia hai chiu suy ngh, c nn  tim th thn thc mt ln na hay khng."},{"name":"Hoa linh lan - Go","content":"<h2> i li tc gi: </h2>Hoa Linh Lan, khng bit bao nhiu ngi c s hiu c tm tnh su kn m mnh gi gm trong . u cng chng c g qu su sc, n gin ch l cm gic lc lng khi mnh khc vi mi ngi. V ci s khc bit khng mong mun y, m v tnh b nhng k xung quanh ghen ght v k th. Nhn vt chnh ca mnh trong Hoa Linh Lan ln ln trong ci tui th oan ung , gia nh nh ni b ri, b bn b xung quanh xem nh qui vt. V mt cht thiu st bm sinh, m sut nhiu nm lin sng trong hoang mang v lo s. Anh y c mt ngi m, v i nh mi ngi m trn th gii ny, giang i vng tay yu t, bo v v bao bc con trai mnh bng tt c yu thng. H c chy trn, cn d lun c ui theo h. H  ly ming li ngi i, chng khc no dng tay khng chng vi gm ao. Ming i va hi va bn, lng ngi va lnh, va tanh. Ni vy, chc cc bn ngh Hoa Linh Lan hn s chm trong ni m nh bi quan, y bt hnh? Nhng n hon ton khng kt thc nh th. Mnh nhn ra vai tr ca mt ngi vit, chnh l s thit tha bnh yn v mang n nim hy vng cho cuc i. Nhn vt ca mnh lm li nhng nm thng sng trong v vng,  cui cng khi cn k sinh t, tnh yn v hy vng  cu sng cuc i anh y.","quote":"C ai ng c mt ln vp ng  i, c th thay i cuc i ca bit bao nhiu con ngi sau ?"},{"name":"Hy chm sc m - Shin Kyung Sook","content":"'Hy chm sc m' ta ra mt th hng thm m hoc , vi ni bun trong sng, m nh, cm ng l thng. Mt ngy, c gia nh xo trn v M bin mt. M b lc khi chun b bc ln tu in ngm cng b  ga Seoul. Hai ng b d nh ln y thm cu con c. Ngi chng mt i lun i nhanh hn v na bc chn. Ngy hm , nh l thng, ng  vi v ln tu m qun mt v mnh vn cn  li pha sau. Khi i tm mt tm nh  dn ln t ri thng bo tm ngi lc cng l lc nhng a con pht hin ra rng, trong ngi nh chng h c bt c tm nh t t no c th dng cho vic . Trong nhng tm nh gia nh, mt iu l m by lu nay h khng  ,  l bng dng ngi m ca h, lc no cng ch nht nha ng pha sau. Gi bit tui th  trng thnh l mt hnh trnh  c nh sn cho tt c mi ngi. Gia nhng thng ngy y, chng ta mi m ui theo trm th o mng tnh yu. Danh vng, tin bc, quyn lc, khao kht c khng nh bn ng ca mi c nhn. Chng ta c ng mnh  c tt c, hiu ht mi gc cnh ca tnh yu trn th gian. Thc ra chng ta khng h bit, gia vng quay ca s phn, mnh  nh mt i nhng g p  nht, tnh yu ton vn v ln lao nht.Hy chm sc m l mt cuc hnh trnh nh th. ","quote":"Ch c ngh chng ta s li c  bn m d ch mt ngy thi khng? Ch c ngh em s li c thi gian  hiu m na khng,  lng nghe nhng cu chuyn ca m,  an i c m xa m  chn vi  u  trong dng thi gian?"},{"name":"Cho i l cn mi - Azim Jamal & Harvey Mckinnon","content":"Chng ta ang sng trong mt th gii y bt trc vi nhng ni au ca nhn loi cng s hu hoi nghim trng ca mi trng. Chnh v vy, chng ta cn quan tm n nhau nhiu hn  gp phn gim bt nhng ni au v bt trc y trong cuc sng. Th nhng, chng ta s th hin iu  nh th no  c th lm vi i nhng bt hnh ca chnh mnh v ca ngi khc? B quyt n gin nht chnh l:'Mi ngi hy bit chia s, bit cho i, bit trao tng nhng g tt nht m minh c cho ngi khc'.<br/> Tht vy, cuc sng chnh l qu trnh trao tng v n nhn khng ngng. C rt nhiu th chng ta c th trao tng cho ngi khc, t ca ci vt cht cho n mt li khuyn, mt tri nghim, mt li ni chn tnh, hay thm ch ch l mt nh mt thin cm, mt n ci n hu... Nhng hnh ng chia s  th hin tm lng yu thng ca chng ta dnh cho mi ngi. V chc chn rng chng ta cng s n nhn c khng t iu tuyt vi bt ngun t s cho i cao p ny. Cuc sng l qu trnh trao tng v n nhn khng ngng, mi chng ta l mt mc xch quan trng trong vng lin kt y. V vy, bn ng do d khi m rng lng mnh vi mi ngi. Rt nhiu ngi, rt nhiu ni trn th gii ang ch i  bn mt s ho tm, mt vng tay m p..... ","quote":" ng lc ca s chia s hon ton l do t nguyn m khng cn n p"},{"name":"L ri trong thnh ph - L Xun Khoa","content":"'L ri trong thnh ph' l nhng mnh ghp tm t, tnh cm, tnh yu, tnh bn, kht vng sng v nhng vc thm tm hn ca ngi tr an ci gia mt cu chuyn v m nhc c k theo mt b cc c o, khng theo trnh t thi gian. Tc phm khng ch cho c gi ci nhn v th h thanh nin Vit Nam trng thnh trong thi k ton cu ha, m cn im li mt s du mc pht trin ca cng ng mng, phong tro nhc nh, thp thong u  nt i thay ca H Ni, th  vn hin vi nhng con ngi va c va mi, im li nhng s kin nh giai on chun b bt buc i m bo him trn c nc hay trn lt lch s nm 2008, thng thc c khng kh m m ca thi k khng hong kinh t bao ph cc doanh nghip ko di t 2011 n hin ti. <br /> Nhng ai ang tr v  tr s tm tm ci khi bt ng soi thy nhng bi ri, di dt, nhng lc thnh cng, nhng ln vp ng ca chnh mnh trong cu chuyn ","quote":"Ma ng xa nhau, bit bao nhiu cn co nhung nh. Ma ng gp nhau, kht khao c gn nhau hn"},{"name":"Ngi n ng mu cu hnh phc - Laurent Gounelle","content":"T khi xut hin trn tri t cho n nay, nhn loi mi mit i tm hnh phc. Oi om thay! Khng phi ai cng tm c. Tc phm Ngi n ng mu cu hnh phc ch ra mt phng php tm hnh phc kh n gin, hiu qu nhng r rng i hi s n lc ln trong vic thay i cch sng, thay i thi quen.<br/>Nhn vt chnh ca quyn sch ch c hai ngi.  l Julian - cng l ngi xng ti trong tc phm v mt ngi n ng lung tui c tn l Samtyang. Julian l ngi phng Ty, l khch du lch; cn Samtyang l dn gc ca o Bali, c xem l mt thy cha bnh, nhng trong tc phm ng l mt ngi thy trn nhiu phng din. Vi cch vit gn nh ghi nht k v t trng tm vo nhng li i thoi, nh vn Laurent Gounelle  to s hp dn, li cun ngi c t u n cui.  vit c quyn sch ny, chc tc gi phi nghin cu nhiu v tn gio, trit hc, tm l, ngn ng...; bi v sc hp dn ca quyn sch khng phi l nhng tnh tit gay cn, ly k m l nhng kt lun rt th v c rt ra trong khi hai ngi i thoi vi nhau.<br/>Nhng ai c quyn sch ny, c th t tm thy nim tin, thy s cn thit phi thay i chnh mnh  bt u cch sng th v v  ngha hn.","quote":"ng  ai hay bt k ngi no ni cho anh nghe nhng iu anh khng c kh nng thc hin iu g . Bi chnh anh mi l ngi chn cho mnh cuc sng v sng vi n."},{"name":"Khng gia nh - Hector Marlot","content":"'Khng gia nh' k v mt em b khng cha m, khng h hng thn thch, i theo mt on xic ch, kh, ri cm u on y i lu lc khp nc Php, sau  b t  Anh, cui cng tm thy m v em. Rmi  ln ln trong gian kh, em  chung ng vi mi hng ngi, sng khp mi ni. Em  lao ng m sng, lc u di quyn iu khin ca mt ng gi tng tri v o c, c Vitali, v sau t lp khng nhng lo cho mnh, cn bo m vic biu din v sinh sng cho c mt gnh ht rong.  c khi em v c on lang thang my hm lin khng c cht g trong bng.  c khi em xut cht rt.  c khi em b lt ngm chn trong ging m mi my ngy m.  c khi em mc oan b gii ra trc ta n v b  t. V cng c khi em c nui nng ng hong, m no... nhng d  u Rmi vn gi cho mnh lng tt v s hy sinh. Cui cng, qua bao gian kh, Rmi gp c gia nh tht s, tr n cho nhng ngi  gip  mnh trong lc c hn, v sng hnh phc. Bn cnh Rmi cn c ch b ngh s Matchia khn ngoan, linh li, tho vt, tn tnh vi bn; Con ch Capi khng nh ngi v rt c ngha; Con kh Gilic lin lu v ng thng <br/>Khng gia nh lm sng li nhng hnh trnh th b m mi chng ta tng c v tng qun lng","quote":"Ti l mt a tr ngi ta nht c. Tuy vy, cho n nm ln tm, ti vn tng ti c m cng nh mi a tr khc, v mi khi ti khc th lun lun c mt ngi n b du dng m ti vo lng, u ym ru, khin cho nc mt ti ngng chy."},{"name":"Nh gi kim - Paulo Coelho","content":"Nhn vt chnh ca cun tiu thuyt l mt chng trai tr tn l Santiago. Ln ln trong hon cnh ngho kh, nhng cu lun m c c chu du khp th gii. V b cu ng  cho cu dng gia ti ca mnh mua mt n cu. Th nhng, trong nhng gic m ca mnh, Santiago thng thy hnh nh mt a b dn cu n chn Kim t thp v bo rng ni y chn vi mt kho bu ln. V th, cu quyt nh t b n cu  gn b vi mnh sut bao nm  ln ng sang Ai Cp tm kho bu.<br/>L mt cun tiu thuyt vi vn phong gin d, li dn chuyn nh nhng nh nhng cu chuyn c tch thn tin, Nh gi kim  a ta n vi mt trit l su sc. Nu Santiago khng bc tip, i mt vi bit bao him nguy  gii m gic m th liu cu c th tn mt ngm nhn v p k v ca Kim t thp? Liu cu c th tm c mt tnh yu ngt ngo nh c tch vi Fatima? Tt c nhng tri nghim trong chuyn phiu du theo ui vn mnh ca mnh  gip Santiago thu hiu c  ngha su xa nht ca hnh phc. i khi ngi ta phi i c mt cuc hnh trnh di, vi bao him nguy vt v, ch  hiu rng, hnh phc  ngay di chn ta,  ni m ta  tng bc qua.","quote":"Ti khng sng trong qu kh hay tng lai, ti ch quan tm n hin ti. Nu bn c th sng trong hin ti, bn l mt ngi hnh phc"},{"name":"Ngi ua diu - Khaled Hosseini","content":"Nhn vt chnh trong tc phm khng phi l mt cu b hon ho, cng c bit khng phi l 'con nh ngi ta'. Nh nhng a tr ln ln trong gia nh giu c, cu b cng c nhng tt xu. c bo bc t b, cha bao gi phi ng u vi kh khn, cha bao gi phi chin u  bo v nhng g mnh c, cu b cng ch k, cng hn nht, cng c mt ngi bn ht lng v cu nhng li b cu phn bi. Nhng sau tt c, n khi ln ln, sau khi tri qua qu nhiu bin c, ta c th thy, cu b ny cui cng  trng thnh,  dm i mt vi nhng sai lm mnh mc phi trong qu kh,  c th i mt vi s hn nht ca mnh, v  dng cm  chuc ti cho s phn bi ca mnh trc y.<br/>Qua cu chuyn ny, ta cn hc c mt bi hc v tnh bn, v s hi sinh. Thng qua bi cnh ca cu chuyn, ngi c cng hiu thm c phn no v x hi o Hi  Afghanistan, v s mu thun sc tc gia ngi Sunny v Shi'a  ri bn s b shock v cm thy tht bt ng khi bit rng nh kin trong x hi li c th n su vo suy ngh ca con ngi n th. nh kin rng c nhng ngi khi sinh ra  vn thp hn hn nhng ngi khc. Bn cm thy bt cng nhng ri cng cm phc s nhn nhc cng nh s trung thnh ca h. V ri nh ti, c l bn cng s c c th tm c mt ngi bn trung thnh nh vy.","quote":"Chng ti u gy ra ti c v phn bi ngi khc. Nhng b ti  c lm vic tt sau , cn ti th  lm g ngoi vic  li cho ngi m  b ti phn bi ri c qun i mi th m ti  gy ra?"},{"name":"Hai s phn - Jeffrey Archer","content":"Bn c tin rng s phn s dn dt con ngi trn bc ng m h chn hay khng? Abel v Will l nhng con ngi c s phn hon ton i lp nhau. Will sng trong nhung la ca s giu sang v quyn qu, chng mang trong mnh dng mu thng minh tt nh vi nhng con s, l nim t ho ca c dng h; cn Abel l con c nht v nui, b bt, phi vt qua hng ngn cy s  n vi New York. Cuc i h c l cng c nhng lc thng trm, c nhng bin c, c nhng mu thun n tn th h sau, nhng b qua mi th, h li chn cho mnh con ng ring. Hai s phn, hai li sng, hai con ngi mang n cho ngi oc bao suy ngh v cuc sng.","quote":"ng khc, Wilham. Ngi ln ai li khc."},{"name":"C gi chi dng cm - Elfriede Jelinek","content":"Nh ng ci tn ca n, m nhc l mt thnh t quan trng khng th thiu. Tc gi dnh rt nhiu dung lng  vit li nhng suy ngh ca Erika v m nhc, v nhng nhc s thin ti c ngng m, v nhng bn giao hng kinh in i lc nhng din gii chi li v m nhc khin ta dng nh dn lc i khi mch chnh ca cu chuyn khi mi bt u c. Tuy nhin, cng v sau, chnh vic thut li k lng nhng suy ngh ny li gip ta cm nhn c su sc ni c n ca Erika. Erika  lun sng di s qun thc cht ch ca m  cng mt cn h sut qung thi gian di tui tr ca mnh. M c cho rng c l mt thin ti m nhc v b mun hng c tr thnh mt ngi nh th. Vi b, tnh yu hay n ng s l liu thuc c git cht s tp trung m con gi b cn c  hng n tng lai rc r pha trc. V nh th, Erika  gn bc sang tui 40 m vn cha mt ln bit tnh yu tht s l g. Cuc sng hon ton ch c m nhc. m nhc trn ngp khp cc ngc ngch suy ngh ca c. <br/>Sau tt c nhng vic in r, ni lon, nhng hnh ng qui d, n cui cng Erika tht s vn ch mong mt iu gin d nh th.","quote":"iu kin cn bn ca tnh yu l ngi ta cm thy c coi trng, v mt ai  t chng ta ln hng u"},{"name":"Lng nghe yu thng - Susanna Tamaro","content":"'Lng nghe yu thng' ca tc gi Susanna Tamaro l nhng suy t trong hnh trnh i tm li ci r ca chnh mnh ca mt c gi tr. C ch bit t 4 tui, mnh sng vi b ngoi. Ngoi ra, cha m mnh l ai, c khng h c c mt thng tin g. B ngoi c  che du tt c. B  dng nn nhng cu chuyn p  cho chu yn lng v xut x ca mnh. Bi b s s tht s khin chu b tn thng. Nhng truyn c tch ch c tc dng lc c chu yu cn nh, v khi  ln hn,  hiu ra phn no s tht m ngi b che giu, mi yu thng trong c chu gi  chuyn sang thi cc tnh cm khc hn - lng cm ght. Mi th ln ti nh im khi ngi b qua i. C bt u hnh trnh i tm ci r ca mnh. C  pht hin ra cha mnh l ai, m mnh l ai. V hn th na, c nhn ra rt nhiu iu v quan nim sng, gi tr sng.<br/>y khng phi l mt cun sch d c. Tit tu ca n khng nhanh. Nhng b mt c chm ri hin dn ra trong hnh trnh khm ph ca c chu gi. Th nhng ngi c s vn c cun i, bi trong tng li vn, tng trng on l nhng xc cm mnh lit m ta c th cm nhn: Ni au ca s c c, ni s hi mt i ci g  ca bn tnh ch k c hu trong mi con ngi. Kht khao c yu, c hnh phc nhng b chi b.","quote":"Nhng iu nh nht nht li chnh l b n ln nht... Nhng iu to tt nht thng nm m h trong ci gi l gii hn, nh ngha"},{"name":"Ai ri cng cht - Atul Gawande","content":"Con ngi t khi sinh ra chnh l ang t t bc nhng bc u tin n gn vi ci cht, bn s chng th no bit trc c cuc hn vi thn cht l khi no, c th n s n t ngt m bn chng bao gi ng ti. Ci cht xut hin trc mi mt ngi mang mt din mo khc, cng ging nh chng ta c mt din mo ring khi xut hin trc mi ngi. Mi ngi n ng s v tri theo cch m n tri cng khng bit, mi ngi ph n s i n cui chng ng theo cch ring ca h.<br />Trong nhng cun sch ca mnh, Tc gi - thn l mt bc s phu thut  dng ngi bt mnh m khng cht s hi ca mnh tit l cho chng ta bit s tht ng sau ngnh y cng nh nhng cuc chin m cc thy thuc nh ng phi i mt v tranh u vt qua. Ln ny, vi tc phm Ai ri cng cht!, ng phi by cho chng ta thy nhng gii hn v nhc im ca ngnh y - trong c chuyn mn ca ng ln nhng chuyn ngnh khc - khi cuc sng con ngi b e da bi s lo ha v ci cht. Qua , ng cng ng thi khm ph ra rng mi chuyn u c cch gii quyt, rng chng ta hon ton c th lm khc i,  mang li cuc sng tt p hn cho mi ngi cng nh cho chnh bn thn chng ta.<br/>Mi con ngi sinh ra khng phi ch  n, ng hay tn ti qua ngy, m chnh l  c sng mt cuc sng ng ngha; rng mc ch cui cng ca y hc khng phi  ko di s tn ti v ngha ca con ngi, m chnh l  gip chng ta c mt cuc sng mi mi ong y hnh phc - cho n tn pht lm chung!","quote":"Con ngi cm thy cuc sng ca h c  ngha khi v ch khi h c quyn t vit nn cu chuyn i mnh"},{"name":"C gi n t hm qua - Nguyn Nht nh ","content":"'C gi n t hm qua' vi nhng cu chuyn tnh yu tr con tuyt nhin trong sng. Bng hai dng hi c, cu chuyn  a ngi c tr v qu kh ca Th - mt cu con trai rt t ho khi ngy nh lc no cng bt nt c con b hng xm Tiu Li ngc nghch. Ri hi c li a Th tr v vi thc ti kh s bi s hnh h ca ti con gi, c bit l Vit An. <br/>Sng vi thc ti l th, lun ht mnh v c bn Vit An l th, nhng nhng dng hi c ca Th vn a cu tr v ngy xa, ni c nhng k nim tht p bn c bn u i v duy nht (sau ny c thm Vit An) l Tiu Li. Hai dng thi gian qu kh v thc ti c xen k nhau khin Th v c ngi c mun ht mnh vi hin ti l Vit An nhng li tic nui qu kh l Tiu Li. C l bi Tiu Li ra i vi vng khin Th khng kp n nhn. i lun v khng bit khi no mi gp li, s chia ly vi v  li cho Th bit bao nhiu ht hng v nui tic. <br/>Ngi ta ni, 500 ln ngonh mt nhn nhau mi i c mt duyn gp g. Bt c ngi no chng ta gp cng ng l ngi chng ta cn gp. ","quote":"Con gi cng ln cng khn, con trai cng ln cng ngu"},{"name":"G ca thin ng - Jeffrey Wands","content":"Bn c tng tin mnh c gic quan th 6?<br/>Bn c tin vo th gii tm linh?<br/>Bn c tin vo s trng hp ngu nhin hay  l sc mnh tm linh khin khi mun mt iu g  th t dng n xy ra?<br/>Khng cn phi theo mt tn gio no nhng bn vn c th khm ph ra sc mnh tm linh ca bn thn. Tc gi Jeffrey Wands l mt nh tm linh ni ting, ng c kh nng kt ni vi nhng ngi  khut v lm thay i cuc sng ca nhiu ngi. ng  c kt kinh nghip ca bn thn  rt ra c 6 cha kha tm linh gip bn sng cuc sng ti p hn.  m c rng kho bu trong cuc i cn c chia kha, v cha kha  m n chnh l trt b i tt c mi ni s  tin vo kh nng thay i, kh nng to ra s thay i ca chnh bn thn mnh.","quote":"Ti bung b nhng ni s ny. Ti khng cn phi s thay i  t c nhng iu ti mun trong i."},{"name":"B gi - Mario Puzo ","content":"'B gi' l ng trm Don Vito Corleone, ngi gc Sicily, mt trong nhng th lnh ti cao ca gii Mafia M. K th cm ght ng, khip s ng, khinh b ng; ng nh con rn h mang nguy him c i mt cha ng mt uy lc ti thng nhn thy ht, bit ht, lm c ht. Nhng i vi bn b, thn quyn, 'B gi' gn nh l mt ng ton nng c th cu h thot khi nhng th kt v ni oan c m ngay c lut php cng chng g c. ng ng l 'Mafia' theo ci ngha nguyn thy ca n thu ban u hnh thnh, ni n nu. <br/>Mario Puzo  ph tan mi suy ngh o tng trc y v gii mafia, v s ra i n. Tht thin cn khi ngh rng mafia l mt l ch bit git ngi, cp ca, bn ma ty, v kh, khng c u c v kht mu tn bo. Khng, h l mt  ch, mt giang sn ring m mi ng trm u ti nng xut chng chng khc g nhng v lnh o, thng c c.<br/>Mt ngi chng chung thy vi v, mt ngi cha yu thng cc con, mun cc con i hc, tr thnh ngi c ti thay v k nghip gia nh, mt ngi bn tin cy, mt ng ch lun sn sng gip , lun sn sng cho i trc khi nhn v min l n khng vi phm nhng nguyn tc ca bn thn. Don Corleone s cho bn thy l mt mafia nhng khng h ng n ph n, khng bt nt n b, KHNG MA TY. ng xy dng nhng nguyn tc ring, tch ra khi ci x hi thi nt v y ry bt cng.","quote":"A man who doesnt spend time with his family can never be a real man!"},{"name":"Th Gii Phng - Thomas L. Friedman","content":"Mt cun sch nht nh phi c! c bit nu bn l ngi cha c nhiu kin thc, ch cp nht thng tin bng nhng mu tin ri rc tnh c thy trn ti vi/ bo ch. Cun sch s kt ni tt c nhng s kin vi cc t kha m chc chn bn  t nhiu 'nghe thy  u '.Cun sch khng ch l kin thc tng hp dng s kin lin quan n vic th gii phng ra nh th no vi s pht trin ca cng ngh thng tin, xut khu vic lm, m cn l cc vn  kinh t, chnh tr lin quan n n. Khng ch l outsource, off-shoring, m cn l Bc tng Berlin, cho ti s kin 11/9, nc M v Bin ladel, cho n Trung ng v du m. ","quote":"o c c th c nui dng trong giu c v khn ngoan ca tr tu v hnh nh kh bo v c trong ci ngho v lc hu."},{"name":"Qung gnh lo i v vui sng - Dale Carnegie","content":"Cc nguyn tc c bn  loi b s lo lng. Nguyn tc 1: Hy ng cht nhng cnh ca nng n dn n qu kh v tng lai. Hy sng vi ngy hm nay, tn dng ti a 24 gi qu gi ca mt ngy. Sao bn khng t hi bn thn mnh v i tm cu tr li cho nhng cu hi sau? Liu ti c ang lng trnh cuc sng hin ti v c mi lo ngh cho tng lai hay m tng n mt vn hng huyn o  tt tn chn tri? Liu ti c lm u m ngy hm nay ca mnh bng nhng hi tic v nhng iu  qua? Liu mi sng thc dy, ti c quyt tm sng trn ngy hm nay  s dng trit  24 gi m cuc sng em  ncho ti? Liu ti c th sng tt hn khi chn cch sng trong ngn kn ca hin ti hay khng? Khi no ti nn bt u? Tun sau? Ngy mai? hay chnh Hm nay? Nguyn tc 2 : Gii php k diu ca Willis H. Carrier dnh cho bn khi gp phi nhng tnh hung kh khn. T hi bn thn iu ti t nht c th xy ra l g? Chun b tinh thn chp nhn iu ti t nht. N lc ci thin tnh trng xu nht. Nguyn tc 3 : Nhc nh bn thn v ci gi phi tr bng sc khe ca chnh mnh khi chng ta qu lo lng.","quote":"iu quan trng khng phi l bit c iu g xy ra trong tng lai m l bit c cn phi lm g  hin ti!"},{"name":"i tm l sng - Viktor Frankl","content":"Trong cuc dit chng Do Thi ca c Quc X, Frankl  b giam cm 3 nm  cc tri tp trung Auschwitz v Dachau. Mt trong nhng kha cnh cn bn trong cun sch ca Frankl l vn  ca s sinh tn. Mc d ng  chng kin, v trc tip tri nghim nhiu iu kinh hong v d man, cun sch i Tm L Sng khng tp trung nhiu chi tit vo nhng iu Frankl  tri qua m nhn mnh nhiu hn v vic thi gian sng di ch  ca Pht xt c  cho ng thy kh nng sinh tn v chu ng phi thng ca con ngi trong nhng iu kin khc nghit nht nh th no. Na u ca cun sch n lc tr li ch mt cu hi: 'Tng ngy sng trong tri tp trung c phn chiu trong tm tr ca mt t nhn ra sao?'. Frankl a ra nhng trng hp ca nhng t nhn tm c hi vng v  ch  tip tc sng d di nhng s tra tn k c v th xc v tinh thn, tt c nhng iu m, i vi Frankl, l minh chng cho tm quan trng ca nhng g c bit n nh l vic tm ra  ngha sng bt k hon cnh c nhn. Trong na sau ca tc phm, Frankl miu t cc liu php trit hc ca mnh. ","quote":"i tm l sng l ng lc chnh yu ca con ngi v n khng phi l s hp l ha ca cc xu hng sinh tn bn nng"},{"name":"Hnh Trnh V Phng ng - Dr. Blair Thomas Spalding","content":"Bn c nh c mt th gii mi  mt th gii khng tng v tim nng phi thng ca con ngi  qua nhng tri nghim, tm hiu ca tc gi sau qu trnh nghin cu di ngy. ng Ty kt hp,  khoa hc Minh trit hi ng,  hin i trung ha vi c xa,  t tri ha quyn, lm th gii tr nn hi ha hn, rng m, diu k hn v nhn vn hn. Vi nhiu nm tm ti, nghin cu v chng kin nhiu vn  v tm linh  gip tc gi vit nn nhng trang sch v cng  ngha, cung cp cho c gi nhiu kin thc qu gi v hn ht l cch nhn nhn v cuc i mt cch su sc hn. Xuyn sut cun sch l nhng cuc i thoi chn tht gia nhng v thin s vi cc gio s, cc nh khoa hc danh ting nht mang n cho ta mt ci nhn tri rng khp bn phng. Nhng nh thng thi y  mang cu chuyn tm linh tng chng rt mng lung, kh hiu thnh nhng cu chuyn hp dn v ti hin li kh r nt. iu ng li nhiu nht c l l v lut nhn qu, v ci sng v ci cht. Khi con ngi ta hiu v khoa hc phng ng ny, con ngi ta s sng mt cuc sng tt p hn, an yn hn, hnh phc hn.","quote":"Phng ng c nhng chn l quan trng ng  cho ngi phng Ty nghin cu, hc hi.  n lc ngi phng Ty phi quay v phng ng  tr v vi qu hng tinh thn"},{"name":"V cng tn nhn v cng yu thng - Sara Imas","content":"c chp bt bi mt b m Do Thi sinh ra v ln ln  Thng Hi, sch dy con V cng tn nhn v cng yu thng c xem l cm nang gi u ging trong vic gio dc con ci m tt c mi ngi u phi hc tp. Bi l khng c a con bt hiu, ch c a con khng may tip nhn phi mt phng php gio dc khin chng dn tr nn bt hiu. Do , cun sch ny rt ng c i vi cc bc cha m ang tm kim mt cch gio dc con ci tht ng n,  nui dy nn mt c th trng thnh, c lp, c ch kin v gip ch cho x hi. Trong tng trang vit, Sara Imas khng ch k li qu trnh nui dy con nghim khc ca mnh m cn tp hp thnh h thng v cc phng php v k nng gio dc con ci.  cui tng phn, tng chng, cun sch lun rt ra nhng bi hc kinh nghim, cng thc c th  ngi c soi chiu. Qua cun sch, c gi cm nhn c tnh yu thng ca mt ngi m dnh cho con c t trong nhng hnh ng, mc tiu, biu hin, k lut hng ngy ch khng bi dng ra th h n bm, khng o to con thnh k tm thng, khng bin con tr thnh nhng ch g cng nghip. Trn thc t, yu thng khng ng cch cn tn nhn hn l nhng trn n roi.","quote":"ng  con ci thua ngay t vch xut pht"},{"name":"Phi l tr  Dan Ariely","content":"Quyn sch ni v tm tr ca chng ta, chng ta lun hnh ng mt cch phi l tr m c ng l mnh l tr lm. Nu chng ta i mua mt cy bt 10k m ca tim A bn 15k. Chng ta sn sng khng mua i ni khc. Nhng tng t vy, nu chng ta vo ca tim mua ci o 500k, m bit c  ca tim khc bn 495k, ngi ta ng  mua ngay, ch ai thm b cng i ni khc. Nhng c hai u l tit kim 5k, con s  khng khc, vy ti sao la chn ca chng ta li khc bit vy? Hy ngh n nhng mn  khc, v hy nh v nhng khon tin  chi cho vic mua sm. Chng ta c tit kim nh chng ta vn ngh? Cnh ca i vo th gii phi l tr ca con ngi  c Dan Ariely h m mt cch v cng th v trong tng  mc ca quyn sch. Cch vit d c d hiu km theo dn chng l cc cuc kho st, th nghim ht sc thuyt phc. V iu quan trng, l nhng iu phi l tr trong tm l chng ta c tit l khng nhm  ph phn hay ch bai bt c k hnh vi no ca con ngi, m ch  chng ta hiu r hn v nhng quyt nh chng ta vn ang phi a ra hng ngy. Su xa hn, chng ta c th chng li nhng chic by tm l. ng thi c th suy ngm  hiu hn v tm l khch hng.","quote":"Phi L Tr: Khm Ph Nhng ng Lc V Hnh n Sau Nhng Quyt nh Ca Con Ngi"},{"name":"Khng Theo Li Mn - Ellen Singer","content":"Bn l mt a tr 4 tui bng nhin c cho 1 ci ko vi li ni: Nu con c gng gi khng n ci ko trong vng 15 pht th con s c thm 1 ci ko na. Bn s la chn th no? n ngay hay c gng ch i? Theo kt qu mt th nghim, nhiu nm sau, khi tm li nhng a tr  tham gia th nghim trn, cc nh nghin cu nhn thy rng nhng a tr  tng  dnh ko, c i 15 pht na l nhng a tr thnh cng v giu c hn nhng a tr khc. Trn th gii ny, khng thiu nhng ngi thng minh nhng ti sao h li ch l nhng anh ti x bnh thng trong khi nhng ngi khc  cng thng minh nh h, li ng hong l mt ng ch? Khng Theo Li Mn l mt cun sch ni v b quyt  ginh ly thnh cng, ng thi l gii s khc bit gia thnh cng v tht bi.  l s ch i v kin nhn trc nhng li ch tc thi. Mt li khuyn khc Khng Theo Li Mn dnh cho c gi. iu ct yu chnh l hin ti, bn ang lm g  tng lai thu c nhiu gi tr hn vi bn thn mnh thay v c tic nui mt qu kh  n mt ko. c vit di dng nhng cu chuyn xut pht t nhng hnh nh gn gi trong cuc sng, Khng Theo Li Mn d c v thch hp vi tt c nhng ai , ang v s nhn li mnh, mun n lc vn ti thnh cng, c th khng rc r huy hong nhng t c nhng iu tuyt diu trong cuc sng.","quote":"Cuc i i khi ging nh mt gic m, nhng l gic m c tht, khng cn t v v cng khng th trn trnh."},{"name":"Ch cn li 2 ngi - Sandra Brown","content":"Cu chuyn v hai ngi duy nht sng st sau mt tai nn my bay  mt vng bng gi v lnh lo. May mn h  tm c mt ni tr n  i ngi n cu. Nng l con gi ca mt t ph, xinh p,thng minh, hc thc nhng likhng quen vi cuc sng  thin nhin v ang b thng. Chng mt con ngi hoi nghi v ph n, mt cu binh, sng khc nghit nhng li rt n ng. H sng cng nhau trong nhng ngy kh khn nhng chen vo gia h l s khao kht ln nhau khng th by t. n khi h ht nghi k ln nhau th chuyn bay cu tr  n. Tr v vi cuc sng hin i, chng v mun trnh xa cuc sng vn khng vui v g ny  ra i.  li nng vi s nh mong v vi bn tnh c lp ca mnh nng  n tm chng bt chp s khng ng tnh ca b mnh. Happy ending!","quote":"Ch Cn Li Hai Ngi...:)))"},{"name":"Tui th d di - Phng Qun","content":"Tui th d di l mt tc phm truyn di by phn ca nh vn Phng Qun. Truyn c khi tho bn b H Ty nm 1968 v hon thnh trong lu c gia h Tnh Tm nm 1986. Cun truyn xoay quanh cuc sng chin u v s hy sinh ca nhng thiu nin 13, 14 tui trong hng ng i thiu nin trinh st ca trung on Trn Cao Vn. Cun truyn miu t sc tch qu trnh tham gia chin u v hy sinh  tui i rt tr ca hn ba mi thiu nin, tp trung quanh bn nhn vt tiu biu l Lm, Mng, Qunh sn ca, Ha en,... v mt lot cc nhn vt khc nh: Bng da rn, Vnh sa, T dt. Cun truyn c h thng nhn vt kh ging Nhng ngy khi la v mt vi truyn ngn khc, khin ngi c cm gic l c nhiu tc phm khc nhau trong thi k ny cng vit v mt nhm nhn vt c tht. Nm 1988, cun tiu thuyt Tui th d di ca Phng Qun c xut bn v nhn Gii thng Vn hc Thiu nhi ca Hi Nh vn Vit Nam hai nm sau  v  c dng thnh phim.","quote":" khi m n c nhng a tr nh b, gy g, ngy th nht cng tr thnh nhng chin s chng gic ngoi xm ngoan cng."},{"name":"Bt tr ng xanh - -J.D Salinger","content":"Ly bi cnh nc M vo nhng nm gia th k 20, vi ngi k th nht, cng l nhn vt chnh ca truyn l Holden Caufield - 17 tui, cu  k li qu trnh ca mnh trong nhng ngy cu  thnh ph New York sau khi b ui khi Pencey Prep - mt trng d b i hc. Cun sch xoy su vo thc trng x hi M lc by gi v li sng ca nhng thiu nin M. Mt x hi m   sc mi n iu, gi to v hoen . Vt ln trn s bt nh trong li k tc tu v c phn ni lon, th tt thy tc gi  cao li sng t do, c lp bn lnh, ng a dua theo m ng m hy tha nhn thc ti, sng l chnh mnh - chn thnh, bn lnh v y yu thng.","quote":"Cho d bn c mt triu nm  lm i na bn cng khng th no xo sch ni mt na s cu ' m' trn th gii ny!"},{"name":"Cuc cch mng mt-cng-rm","content":"25 tui, khi cht ng ra mi th trong cuc sng u quay v h v, Fukuoka b d cng vic kim tra cy trng ti Cc hi quan Yokohama, quay v lm vn ti trang tri ca cha,  thc hnh v chng minh cch lm nng 'chng-lm-g-c'. ng a ra nguyn tc '4 Khng' trong nng nghip (khng cy xi t, khng dng phn ha hc /phn , khng lm c hay dng thuc dit c, khng ph thuc vo ha cht) vi tm th ca mt sinh linh nh b may mn c tn ti gia th gii t nhin rng ln v y quyn nng. Knh trng v yu mn thin nhin, Fukuoka tin ti lm nng m c gng bung b nhng m chp thng gp  loi ngi. ng nghim ra rng con ngi, vi lng tham v  ca mnh,  v ang ch mun lm ch thin nhin, m tham vng bng khoa hc k thut s bin i thin nhin thnh mt ci thin ng gia trn th, m khng th hiu rng thin nhin t thn n  l mt thin ng. Rng 'khoa hc ch c tc dng lm r rng tri thc ca loi ngi mi nh b lm sao!'. Fukuoka ni nh c C m  ging gii, l: chng ta ngh vn  nm  bn ngoi, nhng thc cht vn  li xut pht t bn trong chng ta. Con ngi lm kh bn thn mnh, can thip vo thin nhin v tt c nhng iu  ch gieo mm mng cho nhng s kh au k tip.","quote":"Khoa hc ch c tc dng lm r rng tri thc ca loi ngi mi nh b lm sao!"},{"name":"Anh chng Hobbit - J.R.R. Tolkien","content":"Anh chng Hobbit l mt tc phm dnh cho thiu nhi nhng by gi, dng nh khng cn nhiu ngi nh ti iu  na. Phn ln chng ta hin nay u cho rng y l mt tp i thnh k o tm vc kinh in, ta nh Thn thoi Hy Lp vy. L mt tc phm dnh cho thiu nhi, nn c nhng nt c trng nht nh ginh cho la tui ny: ct truyn n gin, din bin mt chiu, c s phn chia thin c r rng v mt iu c trng nht, l cc nhn vt nh n t mt x thn tin no  vy. Khng phi v th m ngi ln s nhm chn vi cun sch ny, thc t, hng thp k nay ngi ta vn khng ngng si m n. Chng ta si m tng cnh vt mang mu sc  nng thn nc Anh m tc gi  ly cm hng, chng ta say m mt h thn thoi m tc gi  sng to ra - nh mt v Cha - nhng ngn ng, nhng cu chuyn, v nhng ging loi huyn thoi. V b c thin ti ca Tolkien s nh gc bt c ai tng c The Hobbit, mt chuyn phiu lu bt ng, k v i din vi nhng k th ng s nht n t bng ti, v c nhng k th hung him nht tn ti trong chnh bn thn chng ta. Bi nhng l , Anh chng Hobbit  khng b lu m di ci bng khng l ca nhng ngi anh em Cha nhn ca n. The Hobbit ch khng phi Cha nhn,  t nhng vin gch u tin cho th loi tiu thuyt k o ca th k XX.","quote":"Tt trn dy ni m sng lnh lo. Ti nhng hm su v nhng hang xa. Chng ta phi ln ng trc lc rng ng.  ginh li kho vng ca chng ta  t lu qun lng."},{"name":"Tui tr ng gi bao nhiu - Rosie Nguyn","content":"Bn hi tic v khng nm bt ly mt c hi no , chng c ai phi mt ng. Bn tri qua nhng ngy thng nht nho vi cng vic bn cm ght, ngi ta chng h bn lng. Bn c cht mn ni x tng vi nhng c m dang d,  khng phi l vic ca h. Suy cho cng, quyt nh l  bn. Mun c iu g hay khng l ty bn. Nn hy lm nhng iu bn thch. Hy i theo ting ni tri tim. Hy sng theo cch bn cho l mnh nn sng. V sau tt c, chng ai quan tm. Ti  c quyn sch ny mt cch thch th. C nhiu kin thc v kinh nghim hu ch, nhng iu mi m ngay c vi ngi gn trung nin nh ti. Tui tr ng gi bao nhiu? c tc gi chia lm 3 phn: HC, LM, I. Nhng ti thy cun sch cn th hin mt phn th t na,  l C. Hy c sch, nu bn c sch mt cch bn b, s n lc bn b thi thc khng ngng bi  mun vit nn cun sch ca ring mnh. Nu ti cn  tui i mi, hn l ti s c Tui tr ng gi bao nhiu? nhiu hn mt ln.","quote":"Nn hy lm nhng iu bn thch. Hy i theo ting ni tri tim. Hy sng theo cch bn cho l mnh nn sng. V sau tt c, chng ai quan tm."},{"name":"Cha giu cha ngho - Robert Kiyosaki","content":"Ti chnh c nhn l mt ch  b lng qun  Vit Nam, trong khi  nhiu nc pht trin nh M, Nht Bn,; qun l ti chnh c nhn c ph cp rng ri t gia nh, trng hc n c quan; n cng c coi l mt bc  lm giu. Thc t nhiu i gia, t ph cn tha nhn rng khi ti sn m h tch ly c mt phn l nh bit cch qun l ti chnh tt. Hiu r iu ny, Robert Kiyosaki, nh u t, din gi v tc gi ni ting v lnh vc ti chnh ti M,  pht trin mt quan im kinh t c o lin quan n qun l ti chnh c nhn t hai nh hng tri ngc  hai ngi cha ca ng. Mt ngi cha  cha rut ca Rober Kiyosaki  l mt ngi c hc thc cao nhng ngho v ti chnh. Ngi cha kia  cha ca bn thn v l c vn ti chnh ca Robert  li l mt ngi b hc t lp 8 nhng  tr thnh ngi giu c nht tiu bang vi khi ti sn hng chc triu . Ngi cha rut thng ni: 'Ham m tin bc l ngun gc ca mi iu xu'. Ngi cha nui li bo: 'Thiu thn tin bc l ngun gc ca mi iu xu'","quote":"Nu con tm kim tin v c an ton,  l tt c nhng g con s c. Con c th c tin nhng khng tm thy ngun to ra tin"},{"name":"Nhng Tm Lng Cao C - Edmondo de Amicis","content":"Sau ny c con, ti cng s nh b m Enrico, vit th  tm s v rn dy con mnh. Ti thy l mt cch hay - vit lc no cng lm ngi ta bnh tnh v truyn t khoan thai. Vit l ngh 3 ln - lc ngh, lc vit ra v lc c li. Mt a tr t tm b  tri qua cch giao tip, chia s su sc nh th, sau ny s l mt a tr bit bnh tm. Cch dy tr trong truyn lm ti cm ng - t gia nh n nhng ngi thy. H dy cho tr con lng dng cm, hiu tho vi gia nh, nhn i vi b bn. H dy cho tr con s tn ty trong mt ngh, v s cao qu ca mi ngh. Ti thy mnh cm ng. Ti thy mnh mong l mt a tr  ngi trong lp nghe thy ging bi, v 20 nm sau, lc ti v thm thy, ti s nhn li bi tp lm vn ca mnh c thy xp gn, gi gn vi tt c yu thng.","quote":"Tt c mi ngi u cn mt ln c Tm hn cao thng  ln ln"},{"name":"Ngi Bn Hng V i Nht Th Gii - Og Mandino","content":"y khng phi l cun sch bn hng thnh tht.  l mt cun sch v kinh nghim ca con ngi tri ra v bn c th lm g  tc ng n n. Khng c cch no tt hn  thay i nhng g trong tri tim ca bn l dng nhng nguyn tc m Og Mandino s dng trong cun sch ny. Hy p dng nhng iu  vo cuc sng  tr nn tuyt vi hn trong tt c mi th bn lm. Trong mi on, bn c th cm nhn c ca cun sch ny v trit l ca Og Mandino v cuc sng m tt nhin l tnh yu. ng  nhn vo chn l quan trng  khng ch l mt nhn vin bn hng v i, m cn l mt ngi tuyt vi m bn cn c kh nng yu ngi bn ng hnh ca bn nh chnh mnh. Nu bn cm thy chn nn, tht vng hoc mt mt v s hi khi mp m trc b vc hy dit, hy cm trn tay v c cun sch ny tht chm, khi  tinh thn ca bn cm thy nh nhm v sng sut tm ng n thnh cng bng cch lng nghe s khn ngoan trong tri tim ca bn. ","quote":"Hm nay ti bt u mt cuc sng mi - Ti s kin tr cho n khi ti thnh cng"},{"name":"Cun theo chiu gi - Margaret Mitchell ","content":"Bung cun sch xung, ti vn khng th ri tr c mnh ra khi mi tnh y ngang tri ca Rhett v Scarlett, cng nh hnh nh cui cng ca truyn l mnh t Tara ni Scarlett gi l nh cng l ni kt thc ca mt s khi u rc ri v phc tp. iu g  lm Cun theo chiu gi tr thnh mt tc phm kinh in ca mi thi i?  khng phi l cuc chin tranh khc lit, hay nhng li l trit l su sc rn dy con ngi, m qua tng cu chuyn nh, tng nhn vt, xoay quanh mi tnh kt tinh p  gia hai con ngi y kiu hnh v c tnh: Rhett v Scarlett. Truyn khng a ngi c vo mt th gii y mng o mu hng, m  vch trn mt bc tranh thc t, v tin bc, v con ngi, v cng cuc mu sinh, cng a v v danh vng. Tuy nhin   khng thiu cht th, khng thiu i nhng rung ng nh nhng m m nh,  ngi c sau khi  c xong, d c th bung sch xung, nhng vn mi khng th qun c d m cun sch em li.","quote":"K t gii tuyn Mason-Dixon tr xung min Nam, h chng c mt xng ch to i bc, s l c thp qu t i, chng c mt chic tu no trong khi Yankee c th phong to hi cng"},{"name":"Khi Li Thuc V Nhng V Sao -John Green","content":"Hazel (Shailene Woodley) l mt c gi tr mc bnh ung th tuyn gip  di cn ti phi ang sng l nh trong nhng ngy cui cng ca cuc i. Augustus (Ansel Elgort) mc chng bnh ung th xng vi mt bn chn gi v ni s mt ngy no  mnh s chm vo qun lng. Hai ngi gp nhau ti mt cu lc b dnh cho nhng bnh nhn ung th gp kh khn trong vic ha nhp cuc sng. H tranh lun v vic ai ri mt ngy cng s phi i din vi ci cht v tr thnh bn ca nhau.  l cu chuyn  ly i nc mt ca hng triu c gi trn th gii, ng thi cng l cun sch bn chy nht theo bnh chn ca New York Times v Wall Street Journal trc khi c o din Josh Boone chuyn th thnh phim. Hazel v Augustus khng ch l nhng nhn vt trong mt bi kch tnh yu t t m  tr thnh mt hin tng trong gii tr vi phng chm sng ca nhng Nerdfighter (ngi u tranh vi s phn), vi nhng mt ng thn thuc nh okay (c ngha l always) hay nhng quan nim sng nh Khng c cng xng sn xut iu c no trn i Khng c php mu, quyn nng hay sc mnh t mt th lc thn thnh no, cc nhn vt ca The Fault in Our Stars ch n gin sng v chin u tng ngy vi thn cht  thc hin nt nhng d nh, c m ca mnh.","quote":"Ti mun chia s vi mi ngi v ni s ca mnh  ni s b lng qun"},{"name":"K Trm Sch  Markus Zusak","content":"Di ngi bt ca tc gi, khng ch Liesel m hu ht cc nhn vt khc u sng ng, cun ht v d dng chm n xc cm ngi c. Ngi m Rosa vi tnh yu nng hu n giu sau v d dn v hng lot cu chi nng n. Cha nui Hans Hubermann mang tri tim n hu, gin d v chn thnh. Cu b Rudy ng hnh cng Liesel trong mi tr ng nghch  khu ph. Tnh cm gia Liesel v Rudy gi ti nh n tui th bnh yn, mt tnh bn mc mc v mt tnh yu chm n ca thanh xun. Ti ngh trong i ai cng tng c mt ngi bn nh th. Ti vn thng tm n vn chng nh mt cch tip thm ng lc v nim tin vo cuc sng. K Trm Sch ca Markus Zusak l mt trong nhng cun sch thc s xoa du nhng kh khn, tuyt vng m ti tng tri qua. Mi trang sch l mt con ngi trn tr yu thng, bao dung nhng cng rt mnh m, kin cng. H sng trong bu khng kh nght th ca th chin th hai, di bn tay nng n ca ch  pht xt tn c. Ti tng ngh ch c nhng t nc nh b, yu th mi phi vt v trong mt mt ca chin tranh. K Trm Sch th khc. Nu ngi Do Thi b truy lng, tn st, b xem nh mt ging loi h ng th ngi dn c di s lnh o ca Hitler cng tn t khng km.","quote":"Trong bt c hon cnh no th nhng cu chuyn cm ng v tnh ngi vn mang li nim tin v ng lc mnh m"},{"name":"Ngi Truyn K c - Lois Lowry","content":"Cho d mt ai  tng c nhng li gii thiu ngn gn xung quanh cun sch, khng c cng khng th no cm nhn ht s k l ca n. Ngi truyn k c tuyt vi  tr tng tng v nhng thng ip mang theo.<br/>Mt Cng ng khng c xc nh r v khng gian v thi gian tn ti, ch bit nhng thnh phn ca n c hnh hi ging nh con ngi, v mt vi cu trc cng ging nh con ngi. ng u cng ng c mt Hi ng quyt nh tt thy mi vic, d nhin, vn cn mt th lc cao siu no  hn na, ch huy v tng th. Mi c nhn sinh ra khng bi cha m h, m bi nhng M , nhng ngi ph n c nhn Nhim v sinh n ra b mi cho cng ng.Khi sinh ra, b mi s c Hi ng giao cho mt cp b m nui. Mi nc tui l mt lp ngi ca cng ng khc bit nhau v chc nng, v tr. Ln By, mi a tr s c giao mt chic xe p, chng t mt nc trng thnh. V khi ln Mi Hai, chng s c nhn mt Nhim v ring (tc l mt cng vic  duy tr cng ng). Ri ln hn na, chng s c tch nh, kt hp vi mt ngi khc gii  to nn mt nh mi, nhn hai a b, mt trai, mt gi v lm con. Trong cng ng , tt c mi th u phi cng khai cho d l mt gic m, v tt c u nm trong gii lut cho d l vic phi n ht mt ba im tm. Khng c au kh no, tt c bng lng tri theo mt nguyn tc bt bin. K c s phng thch ai  ra khi cng ng, n Ni Khc vnh vin.","quote":"Rt kh c th sa i lut l."},{"name":"Cha Rui - William Golding","content":"Cha rui m u trc tip vo thi im chic my bay ch nhng a tr ngi Anh i t nn gp s c v ri xung mt hon o khng ngi. K c v qu bom va n tung l iu g m nht trong nhng tm hn non nt ang ho hng lao vo cuc phiu lu y ha hn ca tui nh ti mnh t khng ngi ln, khng lut l, th g cng cha s hp dn v b ca khm ph, ca t do. William Golding  dnh nhiu dng vit, nhiu tm sc  v nn khung cnh hn o y t mu sc, m thanh n mi v, cm gic mt cch t m, rc r nhng sc mu ti mi cng trn s sng v ln khut nhng nt b n thn tin. Ti thc s thch bin trong Cha rui. Khng ch d di v du m, thc ti v k o, bin ca Golding l mt sinh th sng c nhn cch, khi d dn, e da khi li trm t, yu mn pha ln thng xt t ni xa cch. Bin c chng nh ng ngoi quan st, bao quanh m tr v th gii ca chng. Khng nh nhng cuc phiu lu chinh phc y lc quan, hp dn vi nhng gay cn v hi (v cui cng bao gi cng l khc khi hon rn r), Cha rui i su khai thc cuc sng bm tr trn o ca m tr con lt nht ln b khng a no n tui mi bn, mi lm. Lm th no  sinh tn? Lm th no  c cu thot?  l hai cu hi cn bn nht tr i tr li trong sut tc phm, cng l vn  gy ra mu thun ri a n s tan v ca ci cng ng tng nh th ngy, trong sng y.","quote":"Vin , du hiu ca thi k mng mui, ri xung nc, bn phi Henry khong nm mt. [] Trn o ny, nhng iu cm k  qu nh vn su m, du khng l r. Thng b ang ngi xm kia c cha m, nh trng, cnh st, lut l che ch. Mt nn vn minh b tn ph v khng bit g ht v s hin hu ca Roger  nu ly n."},{"name":"Cuc i ca Pi - Yann Martel","content":"Piscine Molitor Patel, hay b gi nhm thnh Pissing cho n khi cu t t cho mnh bit danh Pi  con s 3,14 huyn thoi. Ngu nhin thay, ci tn y cng nhng bin c sau ny  bin cuc i Pi tr thnh huyn thoi. Pi ln ln cng nhng ngi bn trong vn th v c nim tin mnh m vo Thng . Chc hn trn th gii ny, Pi l cu b duy nht theo n ba tn gio: Hindu, o Hi v o C c. Trong con ngi Pi, tn gio cng nh dn tc, nh quc tch v nu nh chng ta u tn th Thng  th ti sao li khng th tin theo nhiu o.S tht  chng minh rng chnh nim tin tn gio c phn k d trong mt ngi khc y ca Pi  gip cu sng st v tn ti m vn gi c s tnh to, thng minh v cng rn sau bin c tng nh c th vt kit mi sinh mng sng. Pi l ngi sng st duy nht trong v m tu Tsimtsum ngy 2/7/1977,  lnh nh trn bin sut 227 ngy vi xung cu h v mt con h Bengal tn l Richard Parker. 227 ngy u tranh v ginh git s sng trn b mt mnh mng ca Thi Bnh Dng. Cuc i Ca Pi l mt cun sch nh, khng qu dy v khng ni bt vi ba sch mu xanh bin mnh mng c n c lm nn cho chic xung cu h. L do s cht c bm rit ly s sng nh vy ko phi l v nhu cu sinh hc   l s ghen t. S sng p n ni s cht  phi lng n, mt mi tnh t v y ghen tung qup cht ly bt c th g n c th ng n.","quote":"Mi vic  i c bao gi din ra nh ta vn tng, nhng bit lm sao. Cuc i em cho ta ci g th ta phi nhn ci  v ch cn cch lm cho chng tt p nht m thi."},{"name":"Nhng Thnh Ph Giy -  John Green","content":"Ngi ta c th cho rng Green l ng hong ca vic bin mt cu chuyn v thanh thiu nin thnh mt th g  ln hn nhng g bn mong i. Vi cun sch ny, ta s theo di cuc i ca Quentin Jacobse, mt hc sinh cao trung sp tt nghip khi cu vi v tm hiu iu g  xy ra vi ngi trong lng thi th u ca mnh. Quentin Jacobsen thm phi lng c bn Margo Roth Spiegelman xinh p thch phiu lu. Th nn khi Margo cy ca s lch vo i cu - n vn nh ninja v triu tp cu vo mt chin dch tr th y to bo - cu lp tc i theo. Qua mt m rong rui, ngy mi n, Q ti trng v pht hin ra rng Margo, vn lun l mt n s quyn r,  thc s bin mt y b n. Nhng Q cng sm bit c rng c nhng manh mi - v nhng manh mi y c  li cho cu. Gp gp ln theo cung ng t khc, cng n gn, Q cng kh nhn ra c c gi m cu tng chng  qu quen thuc y l mt cun sch hi hc nhng khng km phn ng s, v n s khi dy trong bn nim khao kht c tham gia mt chuyn Scavenger Hunt ngy h ca ring mnh.","quote":"Margo lun thch nhng iu b n. C l v qu ham thch b n m chnh Margo  tr thnh mt iu b n"},{"name":"Hong t b  Antoine de Saint Exupry","content":"Cun sch c bnh chn l tc phm hay nht th k 20  Php, ng thi cng l cun sch Php c dch v c c nhiu nht trn th gii. Vi 250 ngn ng dch khc nhau k c phng ng cng hn 200 triu bn in trn ton th gii, Hong t b c coi l mt trong nhng tc phm bn chy nht ca nhn loi. Cun sch k v Hong t b, sng trn hnh tinh ca mnh vi 3 ngn ni la v 1 bng hoa hng cu yu thng. Vo mt ngy khi khng chu c tnh ng nh ca bng hoa hng na, cu quyt nh theo cuc di tr ca n chim m thc hin mt cuc chu du ca mnh. Hong t b bay qua 6 hnh tinh, qua mi hnh tinh cu gp mt ngi: ng vua, ng hm hnh, ng nt ru, nh bun, ngi thp n, nh a l. M mi khi gp xong nhng ngi ny Hong t b u tht ln rng: Ngi ln qu l qu i l lng.","quote":"Cuc i chng ta c l gp hng trm, nghn ngi, nhng ch c th kt thn vi mt vi ngi,  chnh l s khc bit do s thun ha mang li."},{"name":"Chuyn con mo dy hi u bay - Luis Sepulveda","content":"Cu chuyn bt u trong mt bui sng thanh bnh ti bn cng Hamburg, ch mo mun to ng mp  Zorba ang nm si nng, v tnh, ch chng kin ci cht ca c hi u Kengah. C hi u ng thng cht v dnh vng du do con ngi v  thc  li trn i dng. Zorba  v cng lng tng khi Kengah ra i v  mt qu trng nh ch chm sc. V Zorba  ha vi c hi u Kengah, d bit vic  vt qua kh nng ca mnh, dy ch chim hi u nn tp bay.  thc hin trn vn li ha, Zorba  cng cng ng mo  cng Hamburg ht lng chm sc cho qu trng.  bo v qu trng, ch  b ngi trng nh gi l 'mo h'. i vi mt ch mo lun ngoan ngon th y l mt s xc phm. Zorba  hi sinh danh d ca mnh  dm bo s an ton cho qu trng. Khi ch chim non m v c trng ra i cng l lc kh khn t ln vai Zorba nhiu hn. Ch mo  phi chui xung ni cng rnh bn thu thng lng vi bn chut  ch chim non yn.. Ch phi hc cch yu thng v quan tm mt sinh vt hon ton khc mnh. Ch phi nui nng, chm sc ch chim non m cng ng mo  t cho ci tn Lucky, dy cho Lucky hiu: Tuy ch c cng ng mo chm sc nhng ch l mt con chim hi u ch khng phi mo. V iu quan trng nht m Zorba phi dy cho Lucky, iu vt qua kh nng ca mnh,  l dy hi u bay.","quote":"Khi bn  tht ha v c l do  vin vo th rt c th ln sau bn s li tht ha na. Chnh v vy bn nn c gng gi li ha ca mnh."},{"name":"Khi hi th ho thinh khng - Paul Kalanithi","content":"Paul l mt bc s phu thut thn kinh thc s gii, mi th dng nh ang m rng trc mt anh, chng ng ngay sau  l mt c r au n - bo bnh bin mt bc s tr thnh bnh nhn. Mi th ban u ch nh mt vai tr mi, mt th nghim hay mt cng vic cross-section i vi anh. T gc  mt bnh nhn, nhn sinh quan ca mt ngi bc s c hon thin. Nhng cn 'au' m anh thng m t cho bnh nhn tr nn tht, nu nh trc kia anh ch c bit n chng nh mt tnh t. Anh cht vn bn thn liu rng 'cu cha' c phi lun l iu tt nht, v i khi s cu cha y tr thnh ni m nh ca thn nhn v l ngc t ca bnh nhn... Theo chn Paul, bn s c nghe v cuc sng ca mt sinh vin Y khoa Yale, v nhng gi quay cung trong vin v nhng trn tr ng suy ngh ca anh v s sng cng nh ci cht. Hy ng coi y l mt cun sch, v rng y l nhng li ca Paul - mt ngi hp hi ang c gng  li du hiu cho s 'tng tn ti' ca mnh. Chng s khng mt m, chng s khng vn th. Ngc li, mnh cm thy  cu ch mt ni lng, s ri bi v hn ht l s chp nhn s phn n nao lng. Hy coi y l mt bc th, mt bc th s gip bn nhn ra mnh ang th,  thy trc khi l thinh khng hy nh mnh  sng y.","quote":"Hy  ti sng y, v mai ny c th ti chng cn tn ti"},{"name":"Cha t nhng chic nhn - J.R.R Toliken","content":" ni v b ba cun sch ny, trc tin phi ni n Anh chng Hobbit. y chnh l tin  cho nhng cuc phiu lu k th v a sc thi trong b ba.  Cha t nhng chic nhn, c mt nn lch s v vn ha ca nhng tc hon ton khc nhau. Nu ngi Tin thanh nh, tinh t, th ngi Hobbit li c phn hi th kch v dn d, ngi Ln cng trc, thng thn v con Ngi sau bao kip trm lun, k tr nn cao thng, k li ha  hn. V cn thm nhiu tc ngi khc, nhng ng mt vai tr thit yu th khng th khng nhc n 5 v ph thy, trong  Gandalf  v Ph thy xm, v sau ny l K s trng  ng c v tr c bit hn c. Nhng Cha t nhng chic nhn ln J.R.R Toliken.ni ting khng phi ch nh cch xy dng nhn vt. Tc gi ca b truyn ny l ngi gn nh u tin m ng cho th loi huyn tng, ng thi to cho mnh mt ch ng vng tri v v i trong mnh t ny c qu nhiu l do  lm mt tc gi kinh in vi nhng tc phm kinh in ngay c khi khng cn sng. S thng tho ng n rt nhiu loi ngn ng (nh chng m ng  sng to ra mt loi ngn ng mi trong truyn  h ngn ng Tin), s nghin cu su rng v truyn thuyt Bc u, v c nhng nm thng ging dy ti trng Oxford, Tt c chng to nn bc thy J.R.R Toliken.","quote":"C m i, v cuc i cho php!"},{"name":"Trm Nm C n - Gabriel Garcia Marouez","content":"im th v ca cun sch cn l cch tc gi ni v s bin i ca lng qu bn ra thi i. Macondo m  do ng t Hose Arcadio Buendia thnh lp ban u yn bnh l th nhng khi nhng ngi Digan mang hi th bn ngoi theo cng bc chn h th mi th dn i thay. nh im l s xm nhp ca nn vn minh cng nghip theo con ng st  khin Macondo bc n nhng nm thng huy hong nht ri ngay lp tc l s li tn nhanh chng. Ci cch m Marquez miu t v cng ty Chui - th phm chnh trong cuc n p v thanh trng dn lng Macondo lun khin ti phi bt ci chua xt. Chng hin ln nh mt th lc siu nhin. Th lc m c th khin c ngi lng ngp ng trong nc ma nhiu nm ri li hn hn trong nhiu nm tip theo. Cng ty k l v c c ny hin ln mt cch y ma mai. Trc chng mi phn khng u tht nh b v v ngha. K c l phn khng ca dng h Buendia huyn thoi.","quote":"Mt cu chuyn tuyt vi v tnh yu, v s sng v v con ngi - nhng ngi lun khng ngng tm kim chnh bn thn mnh gia th gian rng ln khn cng."},{"name":"i Gi H  Emily Bronte","content":"Mt tc phm ca th hn, c o rt cch t m v kho lo, c nui nng v thm nhun nhng bn cht ti tm, cay ng ca nh mnh. Heathcliff, ng tht ng thng cng tht tn nhn, trong mt mi ngi ng l con qu xu xa,  hn, ng khinh b. Cm ght n ni khi ng cht cng khng c ai khc thng cho mt ngi sng tch bit vi x hi, ma mnh cp ot ti sn, thm ch th  vi mng sng ca chnh con trai mnh. ng nhn c th no th tr li th y, khng nhng th cn gp hai gp nhiu ln hn. Cuc i ca ng ly lm sung sng khi tha d vi s tr th hon ho ca mnh. Lc gn qua i, ng cm thy chnh mnh tr v nhng giy pht tui tr, ngi ng yu ang ch ng, v c i dng nh ch c vi ngi thng cm, tha th cho mt ngi y ti li.","quote":" u  trong chnh thm tm mi ngi cng c mt i gi h, cn ci v hiu qunh cn c soi sng, d ni y nh sng tt chm nht, l ni hng nng vng sm nht, l ni khi nhn t ng xa n l mt vng t mi  khm ph."},{"name":"Ba Ngi Thy V i - Robin Sharma","content":"Ti  nm tri nhiu tht bi trong hnh trnh i qua nhng thng ngy ca mnh. Th nhng, mi chng ngi cui cng u li chnh l mt bn p a ti gn hn na ti chn l trong tm khm v cuc i tt p nht ca mnh. Cho d ti c thu thp c bao nhiu ti sn vt cht i chng na th ci thng ngi m ti nhn thy trong tm gng phng tm mi bui sng vn y nguyn  ti khng h hnh phc hn v khng h cm thy tt hn t no. Suy ngm nhiu hn v thc trng cuc sng ca mnh, ti bt u nhn thc c s trng rng ngay trong tim mnh. Ti bt u ch  n nhng ting thm th lng l ca con tim, nhng iu ch dn ti ri b ngh nghip mnh  chn v bt u qu trnh tm kim tm hn mt cch nghim tc. Ti bt u suy ngh v l do ti sao ti li  y, trn hnh tinh ny, v nhim v c th ca ti l g. Ti t hi ti sao cuc i mnh li khng c tc dng v cn phi thc hin nhng thay i su sc no  gip mnh i ng hng. Ti xem xt nhng nim tin ct li, nhng gi nh, v nhng lng knh m mnh nhn ra th gii, v ti t cam kt lm sch nhng lng knh khng lnh mnh.","quote":"Chng ta c th d dng tha th cho mt a tr s bng ti. Bi kch tht s ca cuc i l khi mt ngi ln s nh sng."},{"name":"Ting Chim Ht Trong Bi Mn Gai - Colleen McCullough","content":"L quyn sch do ph n sng tc, c bit hn, tc gi vn cha phi l nh vn khi tc phm ny c xut bn. C l v th m nhng tm t, suy ngh v cch ng x ca nhng ngi ph n trong tiu thuyt ny c khc ha v cng chn tht. Ba th h ph n i din cho ba cch ng x khc nhau trc cuc i, t cam chu n u tranh v c lp. B Fiona  m ca Meggie ch thc l biu tng cho nhng ngi ph n gai gc, mnh m  chu ng nhng ng cay ca s phn. Sinh trng trong mt gia nh kh gi v l ngi ph n thng minh, b vn khng thot c s sp t ca gia nh  ri tr thnh mt ngi v ch quanh qun trong x bp v sinh con. c gi s khng nh gi cao nhn vt ny cho n khi chuyn t vng qu ho lnh n trang tri cu  Drogheda. Ti y, gia nh b gp nhiu bin c v cha ln no b tht s gc ng trc s phn d thin nhin khc nghit  cp i ngi chng v ngi con trai b yu thng nht. Tng phn vi Fiona l Justine  con gi ca Meggie vi ngi chng u tin. L mt c gi tr v hin i, c sng theo nhng chun mc o c ring v khng chp nhn an phn theo ci gi l s phn sp t.","quote":"Bi v tt c nhng g tt p nht ch c th c c khi ta chu tr gi bng ni au kh v i"},{"name":" Lo gi m c truyn tnh - Luis Seplveda","content":"Antonio Jos Bolvar Proano  vng t kh cn x Amazonia (vng trng ca Amazon) El Idilio l mt lo gi k l v m truyn tnh. L dn Cng gio  mt ngi lng ngho, lo  cng v n mnh t ny lp nghip t c hn hai mi nm nay, nhng v lo  qua i v st rt t lu, mnh lo  li, lm bn vi tc ngi th dn Shuar ca rng xanh Amazon, lo ging h, nhng khng phi h. Yn  khng c bao lu, cn lc khai hoang ca thc dn phng Ty dn ui nhng con ngi ny ngy mt su thm vo trong rng rm, lo gi v khng git c k th hm hi ngi bn Shuar ca mnh theo cch ca ngi Shuar, nn nh phi ri khi vng t ca ngi Shuar, li thi trn mt cn lu t dng. Gi y, lo tm vui trong nhng cun truyn tnh mt bc s nha mang n mi su thng mt ln, m chm trong th gii lng mn y  qun i s tn c ca con ngi xung quanh. Cu chuyn ca thi hin ti l mt tay th sn mo rng  lt da ht l mo con, bn b thng mo c, v ri b mo ci x xc bn ba rng. Con mo rng ci y vn ngy m lng vng  El Idilio, v lo gi b buc phi i cng tn th trng mang bit danh Cc Nht b tt c mi ngi cm ght tham gia on ngi sn con mo,  bo v c dn vng t. V trn chin cui cng gia lo gi v mo rng  din ra trong cn ma ro thi t thi ct, mt trn chin cng thng v au n n cc , c hai bn u ngang ti ngang sc, v sau tt c, lo gi  chin thng. Lo li tr v vi cn lu ca mnh, vi nhng cun truyn tnh ca hai ngi yu nhau, au kh v ri kt thc c hu,  qun i thc ti tn khc trc mt mnh.","quote":"Ch trong gang tc, ln ranh gia chinh phc v ph hoi, gia yu v th  b bc qua. "},{"name":"i ngi ngn ng ng di  Robin Sharma","content":"Tnh bn bi i ngi ngn ng ng di l mn qu ti nhn c t nhng a bn thn khi ti b mt phng hng, m mt v tng lai, khng bit lm g, ph mc cho s phn. Cm n nhng ngi bn ca ti  bn cnh ti, cho ti bit n  Robin Sharma  i ngi ngn ng ng di. Cuc i bi sau khi c xong cun sch ti  bit bn thn phi lm g, lm th no  tr nn xut sc. Hy yu ly bn thn, yu ly chnh mnh, yu thng mi ngi xung quanh v yu c thin nhin, sc mu cuc sng: 'Ti mun sng nh khng c ngy mai, t ti iu tt nht, yu nhng ngi trong i ti bng mt tnh yu tht s. Ti mun lm trn phn vic ca mnh  ci to th gii. Ti mun sng tht mnh lit Tt nhin ta cn hng th cuc hnh trnh, bc i nh nhng v can trng vi lng t t. Nhng hy hnh ng vi lng say m, s can m v nh mt ngi sng. Hy hnh ng y mnh lit. Tt c nhng ngi v i u nh th'.","quote":"K tm thng ch lo tm cch git thi gian, vn ngoi c ti tm mi cch  tn dng thi gian"},{"name":"Bng hnh ca gi - Carlos Ruiz Zafon","content":"Ly bi cnh Barcelona nm 1945, ngay sau khi ni chin Ty Ban Nha kt thc, cu b 10 tui Daniel ru r thc dy vo mt bui sng, khng th nh c khun mt ca m mnh, ngi  qua i 4 nm trc .  an i con trai, b Daniel dn cu ti Ngha Trang Nhng Cun Sch B Lng Qun, mt th vin chuyn su tm cc cun sch m khng my ai bit n na. Khi Daniel c ni rng cu c th chn bt k cun sch no cu mun, cu i ti mt gi sch xa l v chn mt cun tiu thuyt vi ta  Bng Hnh ca Gi, tn tc gi c ghi l Julian Carax. Sau khi thc sut m  c, Daniel chui ra khi phng ng vo sng hm sau vi ham mun cn co i tm cc tc phm cn li ca Carax. Nhng khi bt u hnh trnh ca mnh, cu nhn ra mt iu k b l ai  ang c gng tiu hy tt c cc n bn ca mi tc phm vit bi Carax - v cun sch cu gi rt c th l n bn cui cng cn tn ti. Trong sut 8 nm tip theo, Daniel tm kim khp thnh ph nhng manh mi c th lm sng t qu kh b n ca Carax v l gii ti sao ai  mun tiu hy tht c cc tc phm ca ng ta. Hnh trnh ca Daniel  dn cu ti nhng b mt en ti nht  Barcelona cht cha bi s la di, in r, chuyn tnh thng au v c n mng. ","quote":"Nhng li khin tri tim ca mt a tr b u c, cho d l do c tm hay ngu dt, chng vn lu li trong k c, v sm hay mun chng s thiu tri tm hn a tr."},{"name":" S c cch, ng lo! - Tu Nghi","content":"Ngi c th di ta, nhng ta tuyt i khng bao gi t di chnh mnh. Khng c iu g l mi mi, k c nhng ni bun cng th! Cuc sng l mun vn nhng ch 'Ng', chng ta khng hc c ch 'Ng', cng khng th on bit trc c n s n lc no. Nhng chng ta hon ton c th hc cch n nhn n, mt cch tch cc v thanh thn nht c th... C ngi s v nhng ng cay ngang tri  i m gc ng, mt ht nim tin sng ln nhu kh sinh tn. Nhng cng c ngi cng bt hnh, cng nghch cnh th ng lc vn ln trong h li cng ln. H nn ht cay ng xung, to thnh mt lc y  bt ra khi vng ly di chn mnh. Mi th u s c cch gii quyt, nt tht no cng s c cch  m, ngi cn n s n v ngi cn i s buc phi ra i. S c cch, ng lo!","quote":"Nu cnh ca ny ng li, bn s chn m tip cnh ca khc hay bt lc trong on trch v vng?"},{"name":" Ai cng c mt khong tri giu kn - Nguyn Khnh Linh","content":"Khng ging nh nhng cun tn vn lng mn khc, Nguyn Khnh Linh (Admin Beautifulmind VN) a vo cun sch nh ca mnh nhng phn tch tm l va sc bn nhng cng y thu hiu, cm thng nh mt chuyn gia tm l thc th v nhng vn  chng ta vn gp phi hng ngy nhng cha tng mt ln ni ra. C nhng cu chuyn tng chng nh tht nh b, tht hin nhin nhng  li nhng vt hn su trong lng n mi nhng nm thng sau ny. Tuy nhin cun sch ny li chia s vi chng ta nhng iu nh b , vi s chn thnh v thu hiu,  nhng nng tru v hnh  vi i. Vi nhiu ngi, vic tri qua mt ngy vi h cng l v cng kh khn bi sng  cuc sng ny i khi vt v hn chng ta tng. Nhng cu chuyn, tm s nho nh, nhng bi th, mu vit trong cun sch ny chnh l nhng lu tr trong qu trnh sng v tn ti y. Khng phi ai cng c may mn vi mt tm tr vui khe, mt cuc sng v t t u lo hay chuyn tnh cm thun bun xui gi. Cun sch ny vit cho nhng ngi khng hon ho, nhng ngi b tn thng, v nhng ngi b x hi cho l yu ui  xoa du nhng ni au, cho d l tm thi, v khin chng ta hiu rng s tn ti ca nhng iu khng may trong cuc sng ny rt c th s l tin  cho s cm thng, thu hiu vi ngi khc. V quan trng nht, rt c th chng ta s hc c cch yu thng bn thn mnh nhiu hn.","quote":"D th no, em hy c hng v pha trc, chm chm, chm nh hi th ca em. Ch cn vic em th, cng l mt bc tin v pha trc ri. Em khng cn phi tin v pha mt tri, em ch cn tin v pha trc m thi."},{"name":" Tr v vng v - Qunh in Seoul","content":"Tr V Vng V l mt cun sch nht-nh-nn-c ca nhng ngi chun b bc vo ngng tui 20, sp sa ng di di trc cuc sng, bn rn theo ui am m v c gi/chng trai trong mng,  ri sau  hc c cc khi nim 'tri tim tan nt' ln u tin, tht bi ln u tin, chia ly ln u tin... Nhng chuyn trong sch 'khng ging vi mt in tch m bn s nhn thy  trn mng; khng c g thn k, khng c g l mt ch c hi ln ngc dng chy c.  n gin l nhng chia s chn thnh, nhng cm nhn ca chnh mt ngi tr - mt c gi tc di vit sch, liu lnh vi nhng quyt nh ca bn thn, nhng chm ch v quyt tm. Ch n gin l nh vy. Nu nh bn chun b bc vo ci ngng 20 th, chc mng bn! Bn c th qun i tt c nhng g ngi ta tng ni khng tt v bn, v nhng li nhic mc v tiu cc. Bn c th tr thnh bt c iu g m bn mun! V nu h vn c nhng  kin v chuyn , ch cn nh rng bn ang hnh phc v vui v n th no","quote":"Hy t chn cho mnh mt cu chuyn tui tr tht tuyt vi, do-chnh-bn-vit nn!"},{"name":"Tr Tu Do Thi - Eran Katz","content":"Tr tu Do Thi l cu chuyn k v mt chng trai Do Thi tn l Jerome. G trai ny c ci v b ngoi, ni theo cch ca ngi Vit Nam chng ta, l kh lc cc, sng lng bng, n mc l lng. Gn nh lc no anh ta cng din c mt chic qun b bc phch, vi  loi o phng in nhng hnh th k d v nhng cu khu hiu nghe rt chng tai m anh ta ang hng hi qung co v tip th. Anh ta cng rt ham m th thao, c bit l mn bng . Th nhng, tri vi ci v b ngoi bun ci , anh ta may mn c c mt ng lc phn u tht c bit, da trn mt v c cc tng nh a bn gia anh ta vi hai ngi bn thn - mt l Itamar Forman, gio s i hc; mt l tc gi, nh vn Eran Katz ca chng ta. Trong vng ba nm, Jerome s tch cc lm n  c mt ti sn tr gi 50 triu -la, ng thi theo hc mt kha Qun tr kinh doanh v thi ly bng Thc s, nhng theo cch thc m nhng ngi Do Thi thng lm.","quote":"Bn phi xo nhng k c v ni au khi u mnh nhng ng qun chng"},{"name":"Tm ng tui 20s - Trn Th Thy Trang","content":"'Tm ng tui 20s' khng phi l quyn sch dnh cho tt c cc bn tr, m dnh cho nhng bn tr  tm thy c am m ca mnh v c lng cu tin, chu hc hi, ham khm ph, dm th thch, chu chp nhn. C th ni y l sch nng cao, chuyn ngnh, khng phi ph thng. Cng nh nhng quyn sch v th loi self-help, Tm ng tui 20s mang hi th v s sng ca nhng li suy ngh tch cc, tt p. Ton b quyn sch l chia s ca tc gi v nhng tri nghim v kinh nghim ca chnh mnh t khi cn l c sinh vin b nh rt r lng l trn gh ging ng n khi tr thnh mt ngi truyn cm hng v c kh nhiu thnh tch ng n, c nhiu ngi bit n v ngng m, xut xoa. Qu trnh lt xc t su thnh bm , nhng kh khn t mi trng bn ngoi tc ng vo v nhng cm xc chi vi ca ngi tr trc ngng ca vo cuc sng c n tc gi c kt li rt chn tht, ng tin.","quote":"Dp khu hiu i, ci bn cn l mt bn k hoch"},{"name":"Li Sng Ti Gin Ca Ngi Nht - Sasaki Fumio","content":"Li sng ti gin l cch sng ct gim vt dng xung cn mc ti thiu. V cng vi cuc sng t  c, ta c th  tm nhiu hn ti hnh phc,  chnh l ch  ca cun sch ny. Chng c ai t khi sinh ra  c ti sn,  c g trong tay. Vy nn bt c ai khi mi cho i u l nhng ngi sng ti gin. C mi ln bn s hu trong tay nhng  dng hn mc cn thit l mt ln bn ly mt t do ca chnh mnh. Gi tr bn thn chng ta khng o bng nhng  dng m chng ta s hu. Nhng  dng ny ch cho chng ta mt cht cm gic hnh phc nht thi m thi. Mang theo nhng  dng hn mc cn thit s ly ht thi gian, nng lng ca bn. Khi nhn ra c iu , tc l bn  bt u tr thnh mt ngi sng ti gin. Nhng ngi sng ti gin lun cm thy vui v, mi l mi ngy. Ci cm gic ny, ti ngh bt c ai cng c th cm nhn c, d bn c phi l mt ngi sng ti gin hay khng, bi bt c ai. Li sng ti gin ca ngi Nht gm c nm chng, trong , chng mt, tc gi s gii thiu cho bn li sng ti gin l g, a ra nh ngha ca anh v n. Sau  anh s a ra l do v sao mnh li theo li sng ny sau nhiu nm sng trong cn phng ca bn thn.","quote":"Cuc sng t  c, ta c th  tm nhiu hn ti hnh phc"},{"name":" Kiu Hnh v nh Kin - Jane Austen","content":"Cu chuyn tnh yu p gia Darcy v Elizabeth. ng nh tn gi, mi chuyn bt u t nim kiu hnh ca Fitzwilliam Darcy v nh kin ca Elizabeth Bennet. Qua cu chuyn lng mng, ta phn no hiu thm v bi cnh thi i v nhng vn  trong x hi ng thi. ng nh nhng li nhn xt trc , Elizabeth qu l mt trong nhng nhn vt n c c tnh nht trong vn hc, c t tin v chnh kin ca mnh, t tin v gi tr bn thn, c i mt quan st v tnh cch mng m, c khng ngi lm xu mnh ch v nhng nh kin x hi lc by gi. Darcy l mt ngi n ng thnh t, t ni v i lc lm l khin cho anh khng c lng mi ngi, nhng n su bn trong con ngi anh l mt ngi c trch nhim v tt bng. Sau nhng mu thun v hiu lm, c hai  tm thy tnh yu dnh cho nhau. N vn s Jane Austen  tht kho khi  cho tt c cc nhn vt n trong truyn tm c ngi  kt hn.  cu chuyn, ti nhn thy i lc chng ta qu t tin vo quan im phin din ca mnh, rng ta khng nn nh gi ngi khc ch da vo nhng gc phin din m ta nhn thy. V ti thc s rt yu thch nhn vt Jane Bennet, ch ca Elizabeth, c lun nhn ngi khc bng con mt bao dung v y tnh cm, c cho rng mi ngi u tt c, t nht l theo mt phng din no y. Hnh phc xng ng vi c ch khi c lun ngh tt v mi ngi m c gp g. Vi nhiu bi hc su sc, Kiu Hnh v nh Kin xng ng l thin tnh s ca nn vn hc Anh.","quote":"Chc chn c mt s km ci trong tt c cc mn ngh thut m cc c thnh thong s dng  thu phc cnh n ng. Tt c nhng g lin quan n khn vt u ng khinh."},{"name":" Nhng K Vng Ln Lao - Charles Dickens","content":"Ti mt vng m ly nc Anh, cu b m ci Pip, bt chp cuc sng ngho kh  l rn, vn khng ngng m c tr thnh mt qu ng. S phn a cu ti nhng cuc gp g k l nht, vi mt tn t trn tri, vi mt b c gi giu c nghit ng chn mnh trong dinh th, vi Estella m sc p ch c th so vi chnh s kiu ngo ca nng. Gia cc cuc gp g  l mt iu cn phi thng hn: Pip c ha hn s c tha k mt gia sn khng l t mt ngi n danh, mt gia sn v i cho php cu bc chn vo gii qu tc nh hng m c.","quote":"Dickens mang n cho c gi mt bi hc tuyt vi v vic cn phi t t vi ngi l bi bn s khng bao gi bit c h thc s l ai."},{"name":"I GIA GATSBY - F. Scott Fitzgerald","content":"Th gii nhng nm u th k hai mi bc vo thi k hng thnh, rc r vi s pht trin nh v bo ca kinh t, nht l nn kinh t M. Ly bi cnh l New York, ni tp trung ca nhng ba tic xa hoa, huy hong, lng ly, di li k ca Nick Carraway, tc phm l nhng cu chuyn xoay quanh cuc i ca Gatsby v hn th l mt bi kch ln ca mt i ngi. Nick khng ch l ngi bn thn, lun ng hnh cng Gatsby, m cn l v bc hon ho ca Fitzgerald  va c th ng vai tr l ngi trong cuc, c mi quan h mt thit vi tt c nhn vt, hiu tm l, suy ngh ca h, va l ngi c tm nhn bao qut, sng sut ni ln nhng nhn nh ca bn thn v nhng vn  nhc nhi ca x hi.","quote":"Cuc sng l mt b i vi v vn nhng vn  t ra buc con ngi hoc l chp nhn thch nghi vi n, hoc l c gng vn mnh vt qua n"},{"name":"Chin tranh v Ha bnh - Lev Tolstoy","content":"Trong nhng ngy nh tr mnh lit cuc chin tranh ph hoi man r bng khng qun ca  quc M xm lc, trn min Bc, sinh vin Vit nam vn vit nhng  ti nghin cu v chin tranh nhn dn, v con ng i tm l tng ca Anry Bnknxki, Pier Bzukhv trong tiu thuyt Chin tranh v Ha bnh. Aragng cho bit:  c mt thi gian  Php, ngi ta khng th i trn xe la m khng thy nhng ngi c Chin tranh v Ha bnh ca Tnxti. Cun tiu thuyt ny c l l tc phm ln nht, cha tng thy, c ngi Php say m vo nhng nm 1942  1943. Do miu t cuc chin tranh yu nc 1812 nh bin c trung tm c  ngha quyt nh khng ch vn mnh nc Nga m ton chu u, miu t s phn ca c dn tc, nhiu tng lp x hi rng ri trong mt thi im nghim trng trong lch s t nc, Chin tranh v Ha bnh mang dng dp r rt ca mt thin anh hng ca.","quote":"Chin tranh v Ha bnh phn nh mt giai on bi trng ca ton x hi Nga, t gii qu tc n nng dn, trong thi i Napolon."},{"name":"Mt M Da Vinci - Dan Brown","content":"Trong khi ang cng tc  Pari, nh biu tng hc ca trng i hc Harvard, Robert Langdon nhn c mt cuc in thoi khn cp lc ti mun. Ngi qun l ln tui ca bo tng Louvre  b st hi trong bo tng, mt mt m kh hiu c tm thy gn thi th nn nhn. Khi Langdon v nh mt m hc ngi Php ti nng, Sphie Neveu, gii m nhng b n k qui ny, h  ht sc sng st khi pht hin ra mt chui cc manh mi c giu trong cc tc phm ca Da Vinci nhng u mi r rng  mi ngi nhn thy nhng li c ngu trang mt cch ti tnh bi ngi ho s. Ph v khun mu ca cc tiu thuyt trinh thm truyn thng, mt m Da Vinci va l bc t ph ni bt, y cht tr tu, va c sp lp mt cch phc tp vi nhng chi tit v nhng tm ti ng ch . T nhng trang m u cho n phn kt khng th on trc v gy ngc nhin, tc gi ca cun sch bn chy nht Dan Brown,  chng t mnh l ngi k chuyn bc thy.","quote":"Mt m Da Vinci l mt tc phm hay vi nhng tnh tit hp dn, hi hp ht sc nhng khng km th v vi bao hon cnh v cng ngn cn treo si tc"},{"name":"B Tc Monte Cristo - Alexander Dumas","content":"Sp c giao trng trch lm mt thuyn trng, chun b ci nng Mercds xinh p du dng, c mt ngi cha ht mc thng yu, c anh em b bn mn phc, tng lai ca chng thanh nin Edmond Dants tht rng ngi hnh phc. Nhng s mnh nghit ng, v nn vin cnh ti sng  ri bi en tt c. ng trong ngy hnh phc nht i, anh b bt giam vo hm ngc lu i khi cha y hai mi tui bi m mu ca nhng k ghen ght,  k v c hi. Sau mi bn nm tri b y a, b cp i tui tr v tnh yu, Edmond Dants, khi vt ngc  hon ton lt xc thnh b tc Monte Cristo nh s gio dc v kho bu b mt ca ngi bn t l nh bc hc  linh mc Faria, bt u hnh trnh tr n bo on.","quote":"Trn i ny khng c hnh phc m cng chng c bt hnh, ch l s chuyn bin t trng thi ny qua trng thi khc. Ch c nhng ngi no  tri qua cnh kh cc mi hng th c cnh sung sng. Ch c k no sp cht mi bit cuc sng l thin ng"},{"name":"Gi qua rng liu - Kenneth Grahame","content":"Gi qua rng liu l cu chuyn gn lin vi tui th ca mnh. Cun sch  vt qua nhng ch trch gay gt ban u v tr thnh tc phm vn hc kinh in dnh cho thiu nhi, xoay quanh 4 nhan vt chnh l ch Chut Chi, anh Chut Nc, thng Cc v bc Lng. Mt bui sng ma xun, Chut Chi cm nhn c c s chuyn ng ti vui ca vn vt v quyt nh di t v i du ngon. Trn ng i ch  bt ng gp Chut Nc - 1 anh chng thn thin, ci m v c mi n sng vi anh trong cn nh nh tin nghi, m cng. Qua s gii thiu ca Chut Nc , Chut Chi lm quen vi bn anh l Cc- mt tn ba hoa khoc lc, ngng cung, h hng  v c tnh kh bc ng, sng xa hoa trong Lu i Cc vn l ti sn ca t tin  li v c bc Lng- mt ngi chn chn, tt bng v ng tin cy.","quote":"T m!  l  ngha ca chng, ca nhng ting gi thn thng, nhng ci vut ve tru mn c truyn i trong khng gian, nhng bn tay v hnh nh b nu ko, tt c v mt hng!"},{"name":"Anne Tc  Di Chi Nh Xanh - Lucy Maud Montgomery","content":"Truyn k v Anne - b gi m ci ng thng v hon cnh, nhng li c mt tm hn lun vui v, trn ngp tr tng tng giu cht th v lng mn  c gia nh hai anh em nh Cuthbert l Marilla, mt b ni tr m ang, mnh m, khng c thi quen th hin tnh yu qua mi ming v Matthew, mt ngi n ng hin lnh, t ni, hi nht gi, tm hn ng iu u tin ca Anne  Avonlea (mc d cui truyn bc Matthew  thi, khng nn spoil) nhn nui sau mt s nhm ln v gii tnh. Bng c tri tim lng thin ca mnh v tnh tnh hot bt ca mnh, Anne  khin khng ch hai ng b Cuthbert, bn b gio vin, ngi quen h hng ca bn b v gio vin, c vng Avonlea, m cn l c mt th h c gi trn th gii, trong  c mnh, yu thng v cm mn. ","quote":"Liu c phi l tt khng khi lun tm ra li mi  phm phi?"},{"name":"Khu Vn B Mt - Frances Hodgson Burnett","content":"Khu Vn B Mt k mt cu chuyn v khu vn b kha kn sut 10 nm tri, cho n khi c Mary, Colin, Dickon nh thc v hi sinh vi tnh yu v s chm sc thn thin. Khu vn sng li cng vi nhng thay i ca nhng ngi xung quanh. Mary khng cn l mt tiu th ngang ngc. Colin r b nhng tuyt vng v sc khe  t ho tuyn b s sng mi. V nh th, trang vin sng bng sc sng con tr bi tnh yu cuc sng. Nhn vt ca Khu Vn B Mt sng ng trong tng trang giy vi nhng nt p ca thi i, vi tnh cch v con ngi lng mn nhng cng y phng khong ca phng Ty.","quote":"Mt cu chuyn v khm ph bn thn, tm thy tnh bn v tc dng hn gn ca thin nhin"},{"name":"Thuyt Phc - Jane Austen ","content":"D khong thi gian tui tr ch c nhc n qua hi c v ton b cu chuyn tp trung vo c Anne trng thnh,  chn chn  t quyt nh cuc sng ca mnh nhng trong lng vn thy m c cho 8 nm ng ng Wentworth phi xa x. Tt c v s ph phim ca mt b bn gi v thi mm lng ca qu c Anne. Nhng mt khc ng l khong thi gian  c lm vy cng khng sai. Cui cng khng th trch ai mnh m ra mun trch tc gi v  vit qu tt, b  truyn ti, l gii th tnh cm gia hai nhn vt chnh v s thay i trong tm t h qu mc tinh t. Mnh dng nh b cun vo s ging co ca h, mt din bin tm l qu hp l ca hai con ngi  tng v n gi vn cn yu.","quote":"Nhng con ng, con ngi, nhng mi quan h chng cho m i khi khin mnh lc li"},{"name":"Nhng Ngi Khn Kh - Victor Hugo","content":"Nhng ngi khn kh l cu chuyn v x hi nc Php trong khong hn 20 nm u th k 19 k t thi im Napolon I ln ngi v vi thp nin sau . Nhn vt chnh ca tiu thuyt l Jean Valjean, mt cu t kh sai tm cch chuc li nhng li lm gy ra thi trai tr. B tiu thuyt khng ch ni ti bn cht ca ci tt, ci xu, ca lut php, m tc phm cn l cun bch khoa th  s v lch s, kin trc ca Paris, nn chnh tr, trit l, lut php, cng l, tn ngng ca nc Php na u th k 19. Chnh nh vn Victor Hugo cng  vit cho ngi bin tp rng: Ti c nim tin rng y s l mt trong nhng tc phm nh cao, nu khng ni l tc phm ln nht, trong s nghip cm bt ca mnh.","quote":"Ti c nim tin rng y s l mt trong nhng tc phm nh cao, nu khng ni l tc phm ln nht, trong s nghip cm bt ca mnh"},{"name":"K Nhc Tung - Donato Carrisi","content":"Nhng hin trng k qui v kinh d khi thi th ca 5 b gi b bt cc ln lt c k st nhn tr li v dn dng vi nhng   ring ca hn. Trn mt sn khu th khng th thiu nhng ngh s, khng ch c vy, ngi nhc tung  sau cnh g cng l yu t ng vai tr quan trng vo thnh cng ca v din. K st nhn l mt ngi nhc tung nh th trn chnh v kch do hn t bin son. Hn th thm rt vo tai nhng con ngi ang b lung lay nhm khi dy trong h th khoi cm quyn lc khi c nm trong tay sinh mnh k khc. Hn cn git dy c c nhng hnh ng ca i iu tra. Bn cnh s git chc, u tr v nhng pha ln gn hi hp c tha trong quyn sch ny, th n cn mang trong mnh nhng trit l su sc   ngi c phi lng li v suy ngm. Nhng b n v s nght th l lc hp dn khin ti i n trang sch cui cng, nhng th khin ti thch n khng phi l v n, cng khng phi qu trnh iu tra, m l nhng miu t tm l xut sc ca tc gi. Nhng miu t n lnh sng lng y bc trn tn n nhng th tn ti trong su thm nht ca con ngi.","quote":"Ci c lun c th c chng t. Cn iu thin th khng bao gi. Bi ci c  li du vt trn nhng g n i qua. Cn iu thin, ta ch c th chng kin thi"},{"name":"Pht Dnh Cho Cha -  Spencer Johnson ","content":"Trong cuc i lm cha, chn hn khng t ngi mt lc no  cht nhn li v nhn ra rng h  qu mi m vi cng vic m qun i l mnh cn c mt t m v nhng a con b bng ang rt cn s quan tm ca ngi cha. Nu tht s yu thng cc con, qu trng cuc sng gia nh, ngi cha s i tm nhng b quyt  to ra hnh phc gia nh. V nhng b quyt trong quyn sch Pht Dnh Cho Cha ca tc gi Spencer Johnson s gip cho nhng ngi cha c cch c x tt nht vi con ci, qua  dy chng bit cch yu qu bn thn, n lc trng thnh v c thi  sng ng n vi nhng ngi chung quanh.","quote":"Tm lng v mt pht quan tm s mang n hnh phc v nim vui."},{"name":"Hn Em Ni Cafe Cupcake - Jenny Colgan","content":"31 tui, Issy b sa thi. Cng vic duy nht gip c nui sng bn thn mnh gi y  tut khi tm tay mt cch t ngt v cay ng. 31 tui, g bn trai m c hng tin tng sau khi bin c tr thnh mt tr a ng ngn v thm hi trc mt bit bao ng nghip  b li c trong y su ca cn khng hong tht nghip. 31 tui, mt vic, mt ngi yu. Issy mt mnh, b v, tuyt vng. C cm thy mnh nh ang ri vo h su ca s bt hnh. Mi kh khn p n cuc i c ch trong mt khong thi gian ngn ngi. Issy chng cn li g ngoi mt c bn y t sng chung nh, mt ngi ng ng mn ang sng nhng thng nm cui i ti vin dng lo.","quote":"Mt cu chuyn ngt ngo, thm lng v y nng lng  khin bn bng tnh  chm tay vo gic m i mnh."},{"name":"Sui Ngun - Ayn Rand","content":"Trong lch s nhn loi, him thy ai pht biu quan im ny v con ngi. Ngy nay, quan im ny hu nh khng tn ti. Tuy nhin, chnh quan im ny  d tn ti  cc cp  khc nhau ca s khao kht, ao c, am m v hoang mang au kh  l quan im khi u cuc sng ca nhng ngi u t nht ca nhn loi. i vi a s h, y thm ch khng phi l mt quan im r rng m ch l mt cm gic m h, kh nm bt, n c to thnh t nhng ni au trn tri v t nim hnh phc khng th din t ni. N l cm gic v mt k vng ln, rng cuc sng ca mt ngi l quan trng, rng nhng thnh tu ln lao c th nm trong kh nng, v rng nhng iu v i cn nm pha trc.","quote":"Mt tiu thuyt trn y sc sng v s th v  mnh m, kch tnh, mnh lit v rnh mch t u n cui  mt tc phm tuyt vi ng  c."},{"name":" n Nht Bn hc v cuc i - L Nguyn Nht Linh","content":"Nc Nht trong mt c gi tr Vit c v s iu l, nhng  l nhng iu khin vng t ca n Nht Bn hc v cuc i hin ln rt tht, rt gn gi. c n Nht Bn hc v cuc i  hiu thm v nhng con ngi Nht Bn qu i lch s, chu o, nguyn tc v duy m n m cc  h  to nn mt nc Nht khin nhiu ngi sng st, ng thi cng l  khm ph t nc xinh p ni bo lu s tnh lng ca truyn thng c xa gia v no nhit hin i, ni th gii t ti su thm vn tn ti gia khng gian pht trin vi tc  vt ngoi sc tng tng ca ngi ngoi cuc.","quote":"cun tn vn c bit v n xoay quanh mt t nc mt nn vn ha ni ting th gii - Nht Bn thng qua lng knh ca c gi nh ngi Vit. "},{"name":"Ngi ph n giu - Kim Kiyosaki","content":"y dng  l quyn sch u tin v u t dnh ring cho ph n, n ph hp vi bt k i tng ph n no c khc vng c lm ch ti chnh c nhn. Cun sch to ng lc mnh m, gy hng th cho ph n tip cn nhng khi nim u tin n kinh doanh v u t. T nhng cu chuyn tng nh bnh thng v nhm chn, ta bt cht nhn ra hnh nh ca mnh ang vng vy vi v s rc ri vi tin bc t chi tiu, tht nghip, gia nh,v chng ta ang mc nh sng vi n, chp nhn nhng kh khan  nh l mt phn au kh hay mt iu hin nhin cn phi tha nhn. Kim cnh tnh ph n, hy thi lm nhng iu ng ngn kiu kt hn v tin, hay duy tr mt cuc sng hn nhn khng hnh phc v khng th c lp ti chnh ,","quote":"Hy sn sng k hoch ti chnh ca chnh bn, ng  tin bc nh mt nim vui v hnh phc ca bn."},{"name":"M, Thm Mt Ci - Cu B ao","content":"Anh nm d mt tun trong bng m, bi khng chu ri khi m. Ti nm thiu mt tun trong bng m, bi mun sm nhn thy m. Thng t nm trong bng m khng thiu ngy no bn nhy ra, bi  hn vi m. Ba anh em, t trong bng m,  yu thng m theo cch ca ring mnh.","quote":"Lng yu thng m vn chng ring ai, tht d lan truyn ti mi chng ta, nn mi khi m cun sch ny ra, tim ta li rung ln nhng nhp xn xang v M."},{"name":" Chuyn phiu lu diu k ca Edward Tulane - Kate DiCamillo","content":"Cu chuyn phiu lu k diu, vi nhng din bin y bt ng khng th on nh trc, khi u c l bng cu chuyn y bt thng ca ngi b k cho c chu gi, c ch nh ca con th bng s - nhn vt chnh ca truyn, trc chuyn vin dng n nhng cuc phiu lu y gian trun, him nguy ca ch th s a ch t th gii di y bin su qua th gii ca bi rc, n vi cp v chng gi cui i, anh em cu b ngho kh, hay nhng ngi lang thang  ri v nm trn gi ca mt ca hiu mua bn (+ sa cha) nhng  chi c. Kt cc chuyn y bt ng, nhng cng y xc ng khin tim ta nh mun tht li, khi mt c b, con ca c ch nh xa xa, cng m vo ca hng  chi v c b chn ngay ch th s ch khng chn th  chi bp b no khc, v ngi m tr, chnh l c ch nh ngy no,  nhn ra ch th s yu qu ngy xa ca mnh sau bao nhiu nm.","quote":"'My c yu qu, quan tm mi ngi tht lng, ht d cha m vi trch mc' - Mt cu ni trong truyn"},{"name":" Nu bit trm nm l hu hn - Phm L n","content":"Nu bit trm nm l hu hn, a ngi c n nhiu tng ca cung bc cm xc, n nhiu khng gian tng chng ta khng th quay v. Va hoi nim, su sc, va gin d, chn thnh, va qu kh, hin ti. Nu bit trm nm l hu hn c th cun ta i mt cch nh nhng v y su lng Vi tp hp 40 truyn ngn, chc hn u  xung quanh nhng cu chuyn y, ta s v tnh bt gp chnh ta. Nu bit trm nm l hu hn l mt quyn sch dnh cho nhiu ngi.","quote":"C th, ta vi v lm, vi v n, vi v ng, vi v yu v vi v sng!"}]

